import React from "react";
import {Note} from "../model/Note";
import {MindMap} from "../model/MindMap";
import {Category} from "../model/Category";
import {Tag} from "../model/Tag";
import {Reminder} from "../model/Reminder";


export type StorageProps = {
    notes: Array<Note>,
    initNotes: () => void,
    mindmaps: Array<MindMap>,
    initMindMaps: () => void,
    reminders: Array<Reminder>,
    initReminders: () => void,
    categories: Array<Category>,
    initCategories: () => void,
    tags: Array<Tag>,
    initTags: () => void
}

let defaultStorage: StorageProps = {
    notes: [],
    mindmaps: [],
    reminders: [],
    categories: [],
    tags: [],
    initNotes: () => {
    },
    initCategories: () => {
    },
    initMindMaps: () => {
    },
    initReminders: () => {
    },
    initTags: () => {
    },
};

export const StorageContext = React.createContext<StorageProps>(defaultStorage);

export function StorageProvider(props: { storage: StorageProps; children: any }) {
    return (
        <StorageContext.Provider value={props.storage}>
            {props.children}
        </StorageContext.Provider>
    );
}

export default function useStorage() {
    let context = React.useContext(StorageContext);
    if (context == null) {
        throw new Error("Storage Context outside provider");
    }
    return context;
}
