import React, {FunctionComponent, useEffect, useState} from 'react';
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import {Dialog, Radio} from "@material-ui/core";
import useStorage from "../../../store/StorageProvider";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import {Category} from "../../../model/Category";
import {CategoryCreator} from "../../screens/Categories";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '100%',
            maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
    }),
);

interface CategorySelectionDialogProps {
    isOpen: boolean,
    onClose: (category: Category) => void,
    activeCategory: Category | null,
    onSelectionChange?: (category: Category) => void,
}

export const CategorySelectionDialog: FunctionComponent<CategorySelectionDialogProps> = (
    {
        isOpen,
        onClose,
        activeCategory,
        onSelectionChange
    }) => {

    const classes = useStyles();
    const [selectedCategory, setSelectedCategory] = useState(new Category("Uncategorized"))

    useEffect(() => {
        setSelectedCategory(activeCategory != null ? activeCategory : new Category("Uncategorized"))
    }, [activeCategory])

    const {categories} = useStorage()

    return (
        <Dialog
            open={isOpen}
            onClose={() => onClose(selectedCategory)}
            maxWidth={"md"}
            fullWidth={false}
        >
            <List className={classes.root}>
                <CategoryCreator/>
                {[new Category("Uncategorized"), ...categories].map((category) => {
                    const labelId = `checkbox-list-label-${category.id}`;

                    return (
                        <ListItem key={category.id} role={undefined} dense button
                                  onClick={() => {
                                      setSelectedCategory(category)
                                      onSelectionChange!(category)
                                  }}>
                            <ListItemIcon>
                                <Radio
                                    edge="start"
                                    checked={selectedCategory.id === category.id}
                                    tabIndex={-1}
                                    disableRipple
                                    inputProps={{'aria-labelledby': labelId}}
                                />
                            </ListItemIcon>
                            <ListItemText id={labelId} primary={category.name}/>
                            <ListItemSecondaryAction>
                                <IconButton edge="end" aria-label="comments">
                                    <MoreVertIcon/>
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    );
                })}
            </List>
        </Dialog>
    );
}

CategorySelectionDialog.defaultProps = {
    onSelectionChange: () => {

    }
}
