import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import useStorage from "../../../store/StorageProvider";
import React, {useState} from "react";
import {Icon, InputBase} from "@material-ui/core";
import {Check, LabelOutlined} from "@material-ui/icons";
import IconButton from "@material-ui/core/IconButton";
import {createTag} from "../../screens/Tags";

const useCreatorStyles = makeStyles((theme: Theme) =>
    createStyles({
        creatorRow: {
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center"
        },
        creatorIcon: {
            marginLeft: "0.8em"
        },
        tagInput: {
            padding: "0.6em 1.5em"
        },
        creatorBtn: {
            marginRight: "1em"
        }
    }),
);

/**
 * Renders A Field For User To Type Tag Name And Create
 * @constructor
 */
export default function TagCreator() {

    const classes = useCreatorStyles()

    const {tags, initTags} = useStorage()
    const [tagName, setTagName] = useState("")

    return (
        <div className={classes.creatorRow}>
            <Icon className={classes.creatorIcon}>
                <LabelOutlined/>
            </Icon>
            <InputBase
                fullWidth={true}
                placeholder={"Create Tag"}
                className={classes.tagInput}
                value={tagName}
                onChange={(x: any) => {
                    setTagName(x.target.value)
                }}
            />
            <IconButton className={classes.creatorBtn} onClick={() => {
                createTag(tagName, tags).then(() => {
                    setTagName("")
                    initTags()
                })
            }}>
                <Check/>
            </IconButton>
        </div>
    )
}
