import React, {FunctionComponent, useEffect, useMemo, useState} from "react";
import {makeStyles} from "@material-ui/styles";
import {UpdateNoteIntoDb} from "../../../model/database/NoteDao";
import {MindMap} from "../../../model/MindMap";
import {createStyles, Menu, MenuItem, Typography} from "@material-ui/core";
import {UpdateMindMapIntoDb} from "../../../model/database/MindMapDao";
import {Note} from "../../../model/Note";
import MainCard from "./MainCard/MainCard";
import {Theme} from "@material-ui/core/styles";
import useStorage from "../../../store/StorageProvider";
import {getSectionedList, SectionFilter, SectionItem} from "../../../utils/MainItemHelper";
import StaggeredGrid from "../commons/StaggeredGrid/StaggeredGrid";
import StaggeredGridItem from "../commons/StaggeredGrid/StaggeredGridItem";
import {StaggeredItemSpan} from "../commons/StaggeredGrid/StaggeredGridModel";
import {MainSelectionBar} from "./MainSelectionBar";
import {TagSelectionDialog} from "../dialogs/TagSelectionDialog";
import {CategorySelectionDialog} from "../dialogs/CategorySelectionDialog";
import {ColorDialog} from "../dialogs/ColorDialog";
import {Category} from "../../../model/Category";
import {Tag} from "../../../model/Tag";
import {Check, DeleteOutline, RestoreOutlined, Share} from "@material-ui/icons";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        menuItem: {
            padding: "0 0.8em"
        },
        itemGrid: {
            padding: "2em 0em"
        },
        itemCard: {
            padding: '0.5em 0.5em'
        },
        sectionItem: {
            padding: "1em 1em",
        },
    })
)

type MainListProps = {
    mainFilter?: SectionFilter
    filters?: Array<SectionFilter>
    onDelete?: (item: Note | MindMap) => void,
    EmptyListComponent: React.ReactNode,
}

export const MainList: FunctionComponent<MainListProps> = (props) => {

    let classes = useStyles()
    const {notes, initNotes, mindmaps, initMindMaps} = useStorage()
    let [checked, setChecked] = React.useState<Array<Note | MindMap>>([])
    let [colorDialog, setColorDialog] = useState(false)
    let [tagSelectionDialog, setTagSelectionDialog] = useState(false)
    let [categorySelectionDialog, setCategorySelectionDialog] = useState(false)

    //Loading Data In Case Updated
    useEffect(() => {
        initNotes()
        initMindMaps()
    }, [initMindMaps, initNotes])

    //Preparing List , Filtering , Sorting , Sectioning
    let items: Array<Note | MindMap | SectionItem> = useMemo(() => {
        return getSectionedList(notes, mindmaps, props.mainFilter!, props.filters)
    }, [notes, mindmaps, props.mainFilter, props.filters])


    //States
    const [anchorMenu, setAnchorMenu] = useState<any | null>(null);
    const [actionItem, setActionItem] = useState<Note | MindMap | null>(null)
    const [actionItemTags, setActionItemTags] = useState<Array<Tag>>([])
    const [actionItemCategory, setActionItemCategory] = useState<Category | null>(new Category(""))

    //Functions
    const onMenuOpen = (target: any, item: Note | MindMap) => {
        setAnchorMenu(target);
        setActionItem(item)
    };

    const onMenuClose = () => {
        setAnchorMenu(null)
        setActionItem(null)
    }

    const updateList = (item: Note | MindMap | null) => {
        if (item == null || item instanceof Note) {
            initNotes()
        }
        if (item == null || item instanceof MindMap) {
            initMindMaps()
        }
    }

    const updateListFor = (items: Array<Note | MindMap>) => {
        if (items.length > 0) {
            if (items.length === 1) {
                updateList(items[0])
            } else {
                initNotes()
                initMindMaps()
            }
        }
        setChecked([])
    }

    const toggleItem = (item: Note | MindMap) => {
        let index = checked.indexOf(item)
        let newChecked = [...checked]

        if (index < 0) {
            newChecked.push(item)
        } else {
            newChecked.splice(index, 1)
        }

        setChecked(newChecked)
    }

    const {EmptyListComponent} = props

    return (
        items.length > 0 ? (
            <React.Fragment>
                <StaggeredGrid
                    className={classes.itemGrid}
                    items={items}
                    render={(item: Note | MindMap | SectionItem, index) => {

                        let itemKey;
                        if (item instanceof Note) {
                            itemKey = "n" + item.id
                        } else if (item instanceof MindMap) {
                            itemKey = "m" + item.id
                        } else {
                            itemKey = "s" + item.title
                        }

                        if (item instanceof SectionItem) {
                            return (
                                <StaggeredGridItem index={index} key={itemKey} spans={StaggeredItemSpan.Full}>
                                    <div className={classes.sectionItem}>
                                        <Typography variant={"caption"}>
                                            {item.title}
                                        </Typography>
                                    </div>
                                </StaggeredGridItem>
                            )

                        } else {
                            return (<StaggeredGridItem index={index} key={itemKey}>
                                <div className={classes.itemCard}>
                                    <MainCard
                                        item={item}
                                        isSelected={checked.indexOf(item) > -1}
                                        openOnClick={true}
                                        onMenuOpen={onMenuOpen}
                                        onSelectDeselect={() => {
                                            toggleItem(item)
                                        }}
                                        onArchiveUnarchive={(item) => {
                                            archiveUnarchiveItem(item).then(() => updateList(item))
                                            onMenuClose()
                                        }}
                                        onPinUnpin={(item) => {
                                            pinUnpinItem(item).then(() => updateList(item))

                                        }}
                                        onTag={(item, tags) => {
                                            setActionItem(item)
                                            setActionItemTags(tags)
                                            setTagSelectionDialog(true)
                                        }}
                                        onCategorize={(item, category) => {
                                            setActionItem(item)
                                            setActionItemCategory(category)
                                            setCategorySelectionDialog(true)
                                        }}
                                        onColor={(item) => {
                                            setActionItem(item)
                                            setColorDialog(true)
                                        }}
                                    />
                                </div>
                            </StaggeredGridItem>)
                        }
                    }}
                />
                <MainSelectionBar
                    checked={checked}
                    setChecked={setChecked}
                    showPinUnpin={true}
                    showArchiveUnArchive={true}
                    onArchiveUnArchive={() => {
                        checked.forEach(archiveUnarchiveItem)
                        updateList(null)
                        setChecked([])
                    }}
                    onCategorize={() => {
                        setCategorySelectionDialog(true)
                    }}
                    onPinUnPin={() => {
                        checked.forEach(pinUnpinItem)
                        updateList(null)
                        setChecked([])
                    }}
                    onTag={() => {
                        setTagSelectionDialog(true)
                    }}
                    onDelete={() => {
                        checked.forEach(removeItem)
                        updateList(null)
                        setChecked([])
                    }}
                />

                <ColorDialog
                    isOpen={colorDialog}
                    onClose={() => setColorDialog(false)}
                    activeColorIndex={actionItem?.colorIndex == null ? 0 : actionItem?.colorIndex}
                    onSelectionChange={(index, color) => {
                        if (actionItem != null) {
                            actionItem.colorIndex = index
                            updateItem(actionItem).then(() => updateList(actionItem))

                        }
                    }}
                />
                <TagSelectionDialog
                    isOpen={tagSelectionDialog}
                    onClose={(tags) => {
                        setTagSelectionDialog(false)
                        let affected = getAffected(checked, actionItem)
                        affected.forEach(item => {
                            item.tags = tags.filter(tag => tag.id != null).map(tag => tag.id!)
                            updateItem(item)
                        })
                        updateListFor(affected)
                    }}
                    selectedTags={actionItemTags}
                />
                <CategorySelectionDialog
                    isOpen={categorySelectionDialog}
                    onClose={(category) => {
                        setCategorySelectionDialog(false)
                        let affected = getAffected(checked, actionItem)
                        affected.forEach(item => {
                            if (category.id != null) {
                                item.categoryId = category.id
                                updateItem(item)
                            }
                        })
                        updateListFor(affected)
                    }}
                    activeCategory={actionItemCategory}
                />

                <Menu
                    id="simple-menu"
                    anchorEl={anchorMenu}
                    keepMounted
                    open={Boolean(anchorMenu)}
                    onClose={() => setAnchorMenu(null)}
                >
                    <MenuItem onClick={() => {
                        if (actionItem != null) {
                            toggleItem(actionItem)
                            onMenuClose()
                        }
                    }}>
                        <Check fontSize={"small"}/>
                        <Typography variant={"inherit"} className={classes.menuItem}>
                            Select
                        </Typography>
                    </MenuItem>
                    <MenuItem onClick={() => {
                    }}>
                        <Share fontSize={"small"}/>
                        <Typography variant={"inherit"} className={classes.menuItem}>
                            Share
                        </Typography>
                    </MenuItem>
                    {
                        actionItem != null && !actionItem.deleted ? (
                            <MenuItem onClick={() => {
                                if (actionItem != null) {
                                    if (props.onDelete != null) {
                                        props.onDelete(actionItem!)
                                    } else {
                                        removeItem(actionItem!)
                                    }
                                    updateList(actionItem!)
                                    onMenuClose()
                                }
                            }}>
                                <DeleteOutline fontSize={"small"}/>
                                <Typography variant={"inherit"} className={classes.menuItem}>
                                    Delete
                                </Typography>
                            </MenuItem>
                        ) : (
                            <div>
                                <MenuItem onClick={() => {
                                    if (actionItem != null) {
                                        restoreItem(actionItem!)
                                        updateList(actionItem!)
                                        onMenuClose()
                                    }
                                }}>
                                    <RestoreOutlined fontSize={"small"}/>
                                    <Typography variant={"inherit"} className={classes.menuItem}>
                                        Restore
                                    </Typography>
                                </MenuItem>
                                <MenuItem onClick={() => {
                                    if (actionItem != null) {
                                        removeItem(actionItem!)
                                        updateList(actionItem!)
                                        onMenuClose()
                                    }
                                }}>
                                    <DeleteOutline fontSize={"small"}/>
                                    <Typography variant={"inherit"} className={classes.menuItem}>
                                        Delete Permanently
                                    </Typography>
                                </MenuItem>
                            </div>
                        )
                    }
                </Menu>
            </React.Fragment>
        ) : (
            <React.Fragment>
                {EmptyListComponent}
            </React.Fragment>
        )

    );
}

MainList.defaultProps = {
    mainFilter: {}
}

//-----Helper Functions
function getAffected(checked: Array<Note | MindMap>, actionItem: Note | MindMap | null) {
    let affected: Array<Note | MindMap> = []
    if (checked.length === 0) {
        if (actionItem != null) {
            affected = [actionItem]
        }
    } else {
        affected = [...checked]
    }
    return affected
}

//-----Item Actions

export async function updateItem(item: Note | MindMap) {
    if (item instanceof Note) {
        return await UpdateNoteIntoDb(item)
    } else {
        return await UpdateMindMapIntoDb(item)
    }
}

export async function restoreItem(item: Note | MindMap) {
    item.deleted = false
    item.archived = false
    updateItem(item)
}

export async function removeItem(item: Note | MindMap) {
    if (!item.deleted) {
        item.deleted = true
    } else {
        item.permanentlyDeleted = true
    }
    updateItem(item)
}


export async function pinUnpinItem(item: Note | MindMap) {
    item.pinned = !item.pinned
    updateItem(item)
}

export async function archiveUnarchiveItem(item: Note | MindMap) {
    item.archived = !item.archived
    updateItem(item)
}
