import {makeStyles} from "@material-ui/styles";
import {AppBar, createStyles, IconButton, Toolbar, Typography} from "@material-ui/core";
import {Close} from "@material-ui/icons";
import React from "react";
import {Theme} from "@material-ui/core/styles";

const useStyles = makeStyles((theme: Theme) => createStyles({
    appBar: {
        [theme.breakpoints.up('sm')]: {
            width: `100%`,
        },
        background: theme.palette.background.default,
        boxShadow: "0px 0px 0px 0px",
        borderBottom: "1px solid " + theme.palette.divider
    },
    menuButton: {},
    selectionTitle: {
        padding: "0em 1em"
    },
    //Custom Styles
    rightContainer: {
        marginLeft: "auto",
    },
}));

export function BaseSelectionBar(props: { children: any, title: string, isVisible: boolean, onClose: () => void }) {
    const classes = useStyles();

    if (props.isVisible) {
        return (
            <AppBar
                position="fixed"
                className={`${classes.appBar}`}
                color={"transparent"}
            >
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={props.onClose}
                        className={classes.menuButton}
                    >
                        <Close/>
                    </IconButton>
                    <Typography variant="h6" noWrap className={classes.selectionTitle}>
                        {props.title}
                    </Typography>
                    <div className={classes.rightContainer}>
                        {props.children}
                    </div>
                </Toolbar>
            </AppBar>
        );
    } else {
        return null
    }
}
