import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import React, {useEffect, useState} from "react";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import useStorage from "../../../store/StorageProvider";
import useLongPress from "../../../utils/useLongPress";
import RemindersListItem from "./RemindersListItem";
import {Reminder} from "../../../model/Reminder";
import {ReminderSelectionBar} from "./ReminderSelectionBar";
import {UpdateReminderIntoDb} from "../../../model/database/ReminderDao";
import ReminderItemMenu from "./ReminderItemMenu";
import {ReminderDialog} from "../dialogs/ReminderDialog";

interface RemindersListProps {
    EmptyListComponent: React.ReactNode
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        remindersList: {
            width: '100%',
            backgroundColor: theme.palette.background.paper,
        },
    }),
);


export default function RemindersList(props: RemindersListProps) {

    const classes = useStyles();

    const [checked, setChecked] = React.useState([] as Array<Reminder>);

    const {reminders, initReminders} = useStorage()

    useEffect(() => {
        initReminders()
    }, [initReminders])

    const toggleCheckItem = (id: number) => {
        let idIndex = checked.findIndex(rem => rem.id === id)
        let newChecked = [...checked]

        if (idIndex < 0) {
            let reminder = reminders.find(rem => rem.id === id)
            if (reminder != null) {
                newChecked.push(reminder)
            }
        } else {
            newChecked.splice(idIndex, 1);
        }

        setChecked(newChecked)
    }

    let longPressEvent = useLongPress({
        onLongPress: (e) => {
            // @ts-ignore
            let id = parseInt(e.currentTarget.getAttribute("reminderid").trim())
            if (id != null) {
                toggleCheckItem(id)
            }
        },
        onClick: (e) => {
            if (checked.length > 0) {
                // @ts-ignore
                let id = parseInt(e.currentTarget.getAttribute("reminderid").trim())
                if (id != null) {
                    toggleCheckItem(id)
                }
            } else {
                //todo opened reminder
            }
        }
    }, {delay: 600})

    const [reminderDialogOpen, setReminderDialogOpen] = useState(false)
    const [currentReminder, setCurrentReminder] = useState(new Reminder())
    const [reminderMenuAnchor, setReminderMenuAnchor] = useState<any | null>()

    const {EmptyListComponent} = props

    return (
        reminders.length > 0 ? (
            <List className={classes.remindersList}>
                {reminders.map((reminder) => {
                    return (// @ts-ignore
                        <ListItem key={reminder.id} reminderid={reminder.id} dense button {...longPressEvent}>
                            <RemindersListItem
                                isSelectionMode={checked.length > 0}
                                isSelected={checked.indexOf(reminder) > -1}
                                reminder={reminder}
                                onShowMenu={(anchor) => {
                                    setCurrentReminder(reminder)
                                    setReminderMenuAnchor(anchor)
                                }}
                            />
                        </ListItem>
                    );
                })}
                <ReminderItemMenu
                    onMenuClose={() => setReminderMenuAnchor(null)}
                    anchorEl={reminderMenuAnchor}
                    reminder={currentReminder}
                    onShowReminderDialog={() => setReminderDialogOpen(true)}
                    onItemSelect={() => {
                        if (currentReminder.id != null) {
                            toggleCheckItem(currentReminder.id)
                        }
                    }}
                />
                <ReminderDialog
                    reminder={currentReminder}
                    isOpen={reminderDialogOpen}
                    onClose={() => setReminderDialogOpen(false)}
                />
                <ReminderSelectionBar
                    checked={checked}
                    setChecked={setChecked}
                    onDelete={() => {
                        checked.forEach(reminder => {
                            if (reminder != null) {
                                removeReminder(reminder).then(() => initReminders())
                            }
                        })
                        setChecked([])
                    }}
                />
            </List>
        ) : (
            <React.Fragment>
                {EmptyListComponent}
            </React.Fragment>
        )
    )
}

export async function removeReminder(reminder: Reminder) {
    reminder.deleted = true
    return await UpdateReminderIntoDb(reminder)
}
