import CssBaseline from "@material-ui/core/CssBaseline";
import {makeStyles} from "@material-ui/core";

import React from "react";

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex",
    },
}));

export default function Container(props: { className: string | null, children: any }) {
    const classes = useStyles();
    let classProp = props.className == null ? "" : props.className
    return (
        <div className={`${classes.root} ${classProp}`}>
            <CssBaseline/>
            {props.children}
        </div>
    );
}
