import {firebaseSignOut, signInWithToken} from "./FirebaseHelper";


import {startDownloadSync} from "./SyncHelper";
import {deleteAllDatabases} from "../model/database/AppDatabase";

const SCOPES = "https://www.googleapis.com/auth/drive.appdata";
const API_KEY = "AIzaSyB_J9dH-BPa5zotdaBL9Ns7vGjF8oml_Fg";
const CLIENT_ID = "319843936727-uofur41kp6kbnv1btm2ifr7j1aalhak1.apps.googleusercontent.com"; //todo client id contains localhost as allowed request origin
const DISCOVERY_DOCS = ["https://www.googleapis.com/discovery/v1/apis/drive/v3/rest",];


/**
 * Called on GAPI Signin State Change To Login Into Firebase & Initialize Sync
 * @param isSignedIn
 */
function onSignInStateChange(isSignedIn: boolean) {
    try {
        // @ts-ignore
        const auth2 = window.gapi.auth2.getAuthInstance()
        if (isSignedIn) {
            const currentUser = auth2.currentUser.get()
            const authResponse = currentUser.getAuthResponse(true)
            console.log("AuthHelper : Singing Into Firebase with AuthResponse")
            signInWithToken(authResponse.id_token, authResponse.access_token)
            startDownloadSync()
        } else {
            console.log("AuthHelper:User Unsigned")
        }
    } catch (e) {
        console.log(e)
    }
}

/**
 * Initializes the GAPI Client For SignIn & SignOut Functions
 */
export function initClient() {
    try {
        window.gapi.client.init({
            apiKey: API_KEY,
            clientId: CLIENT_ID,
            discoveryDocs: DISCOVERY_DOCS,
            scope: SCOPES
        }).then(function () {
            console.log("GAPI Client has been initialized")
            // @ts-ignore
            window.gapi.auth2.getAuthInstance().isSignedIn.listen(onSignInStateChange)
            // @ts-ignore
            onSignInStateChange(window.gapi.auth2.getAuthInstance().isSignedIn.get());
        }, function (e) {
            console.log(e)
        });
    } catch (e) {
        console.log(e)
    }
}

/**
 * Starts SignIn Flow From GAPI
 */
export function signIn() {
    try {
        // @ts-ignore
        window.gapi.auth2.getAuthInstance().signIn()
    } catch (e) {
        console.log(e)
    }
}

/**
 * Signs Out User From GAPI & Firebase
 */
export function signOut() {
    try {
        // @ts-ignore
        window.gapi.auth2.getAuthInstance().signOut()
        firebaseSignOut()
    } catch (e) {
        firebaseSignOut()
        console.log(e)
    }
    try {
        deleteAllDatabases()
    } catch (e) {
        console.log(e)
    }
}
