import {Category} from "../Category";
import {db} from "./AppDatabase";

/**
 * Gets All Categories From Database
 */
export async function getAllCategories() {
    // @ts-ignore
    return await db.categories.toArray()
}

/**
 * Adds a category into database
 * @param item
 * @constructor
 */
export async function AddCategoryIntoDb(item: Category) {
    // @ts-ignore
    await db.categories.add(item).catch(e => {
        console.warn("Error Category To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Adds a category into database
 * @param item
 * @constructor
 */
export async function UpdateCategoryIntoDb(item: Category) {
    // @ts-ignore
    await db.categories.put(item).catch(e => {
        console.warn("Error Category To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Deletes A Category In Database
 * @param item
 * @constructor
 */
export async function DeleteCategoryInDb(item: Category) {
    // @ts-ignore
    await db.categories.where("id").equals(item.id).delete().catch((e) => {
        console.warn("Error Deleting A Category In Db")
        console.log(e)
        console.log(item)
    })
}