export function formatDate(date: number, withTime: boolean = false, withYear: boolean = false): string {
    let d = new Date(date);
    let ye = new Intl.DateTimeFormat('en', {year: 'numeric'}).format(d);
    let mo = new Intl.DateTimeFormat('en', {month: 'short'}).format(d);
    let da = new Intl.DateTimeFormat('en', {day: '2-digit'}).format(d);

    //todo withTime

    if (withYear) {
        return `${da} ${mo},${ye}`
    } else {
        return `${da} ${mo}`
    }
}