import {Note} from "../model/Note";
import {MindMap} from "../model/MindMap";

export class SectionItem {
    title: string = ""

    constructor(t: string) {
        this.title = t
    }
}

export interface SectionFilter {
    name?: string,
    isDeleted?: boolean,
    isPinned?: boolean,
    isArchived?: boolean,
    isPermanentlyDeleted?: boolean,
}

export function filterItems(items: Array<Note | MindMap>, filter: SectionFilter) {
    return items.filter(item => {
        return (
            (filter.isArchived == null || filter.isArchived === item.archived) &&
            (filter.isPinned == null || filter.isPinned === item.pinned) &&
            (filter.isDeleted == null || filter.isDeleted === item.deleted) &&
            (filter.isPermanentlyDeleted == null || filter.isPermanentlyDeleted === item.permanentlyDeleted)
        )
    })
}

export const getSectionedList = (notes: Array<Note>, mindmaps: Array<MindMap>, mainFilter: SectionFilter, filters: Array<SectionFilter> | undefined) => {

    //Filtering With Main Filter / Sorting
    let list = filterItems([...notes, ...mindmaps], mainFilter).sort((item1, item2) => {
        return item2.editedDate - item1.editedDate
    })

    //Filtering & Building Sections
    let sectionedList: Array<Note | MindMap | SectionItem> = [];

    if (filters != null) {
        filters.forEach((filter) => {
            let filteredList = filterItems(list, filter)
            if (filteredList.length > 0 && filter.name != null) {
                sectionedList = [...sectionedList, new SectionItem(filter.name), ...filteredList]
            }
        })
    } else {
        sectionedList = list
    }

    return sectionedList
}
