import {Note} from "../../../../model/Note";
import {useHistory} from "react-router-dom";
import React, {useMemo} from "react";
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import {CardActionArea, CardActions, Chip, IconButton} from "@material-ui/core";
import {MindMap} from "../../../../model/MindMap";
import RichMarkdownEditor from "rich-markdown-editor";
import useAppState from "../../../../store/AppStateProvider";
import {ThemeType} from "../../../Themes";
import {editorDark, editorLight} from "../../../screens/NoteEditor/EditorTheme";
import {getColorForIndex} from "../../../../utils/Helpers";
import {Category} from "../../../../model/Category";
import {Tag} from "../../../../model/Tag";
import useStorage from "../../../../store/StorageProvider";
import {formatDate} from "../../../../utils/DateTimeFormatter";
import {
    ArchiveOutlined,
    CategoryOutlined,
    CheckCircle,
    CloseOutlined,
    LabelOutlined,
    PaletteOutlined,
    UnarchiveOutlined
} from "@material-ui/icons";
import PinFilledIcon from "../../../icons/PinFilledIcon";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import PinIcon from "../../../icons/PinIcon";
import "./MainCard.scss"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            maxWidth: 345,
        },
        selectedCard: {
            boxShadow: "0px 0px 1px 1px rgba(0,0,0,.07)",
            border: "2px solid " + theme.palette.text.secondary
        },
        menuIcon: {
            marginLeft: 'auto',
        },
        cardActionArea: {
            display: "flex",
            flexDirection: "row",
            opacity: 0,
            transition: "opacity .25s ease-out"
        },
        cardIconBtn: {
            padding: "0.4em"
        },
        cardFooter: {
            margin: "0.5em 1em 0.5em 1em",
        },
        chipContainer: {
            margin: "0 0 0.5em 0",
        },
        cardChip: {
            margin: "0.2em"
        },
        footerText: {
            margin: "0 0 0 0.2em",
            color: theme.palette.text.secondary,
            fontSize: "0.8rem",
            display: "flex",
            flexDirection: "row",
            flexWrap: "nowrap",
            justifyContent: "space-between",

        },
        dateText: {}
    }),
);


export default function MainCard(props: {
    item: Note | MindMap,
    isSelected: boolean,
    openOnClick: boolean,
    onSelectDeselect: () => void,
    onMenuOpen: (target: any, item: Note | MindMap) => void,
    onArchiveUnarchive: (item: Note | MindMap) => void,
    onPinUnpin: (item: Note | MindMap) => void,
    onTag: (item: Note | MindMap, tags: Array<Tag>) => void,
    onCategorize: (item: Note | MindMap, category: Category | null) => void,
    onColor: (item: Note | MindMap) => void,
}) {

    const {item, openOnClick} = props
    const {
        id,
        editedDate,
        title,
        text
    } = item

    const classes = useStyles();
    const {categories, tags} = useStorage()

    const history = useHistory()

    let itemCategory: Category | null = useMemo(() => {
        if (item.categoryId !== -1) {
            let c = categories.find(cat => cat.id === item.categoryId)
            if (c != null) {
                return c
            } else {
                return null
            }
        } else {
            return null
        }
    }, [categories, item])

    let itemTags: Array<Tag> = useMemo(() => {
        let t: Array<Tag> = []
        item.tags.forEach(tagId => {
            let tag = tags.find(tag => tag.id === tagId)
            if (tag != null) {
                t.push(tag)
            }
        })
        return t
    }, [tags, item])

    const handleCardClick = () => {
        if (openOnClick) {
            if (item instanceof Note) {
                history.push("/note/" + id)
            } else {
                history.push("/mindmap/" + id)
            }
        }
    }

    const {themeType} = useAppState()
    let cardStyles = {}
    if (item.colorIndex !== 0 && item.colorIndex !== -1) {
        cardStyles = {
            ...cardStyles,
            background: getColorForIndex(item.colorIndex, themeType),
        }
    }

    return (
        <Card
            className={`${classes.root} ${props.isSelected ? classes.selectedCard : ""} main-item-card`}
            variant={"outlined"}
            style={cardStyles}
        >
            <IconButton className={`card-action-btn`} onClick={() => {
                props.onSelectDeselect()
            }}>
                {props.isSelected ? <CloseOutlined/> : <CheckCircle/>}
            </IconButton>
            <CardActionArea onClick={handleCardClick}>
                {/*<CardMedia*/}
                {/*    className={classes.media}*/}
                {/*    image="/static/images/cards/contemplative-reptile.jpg"*/}
                {/*    title="Contemplative Reptile"*/}
                {/*/>*/}
                <CardContent>
                    <Typography gutterBottom variant="h4">
                        {title}
                    </Typography>
                    <RichMarkdownEditor
                        onChange={() => {
                        }}
                        readOnly={true}
                        defaultValue={text.substring(0, text.length > 102 ? 102 : text.length)}
                        theme={themeType === ThemeType.Dark ? editorDark : editorLight}
                    />
                </CardContent>
            </CardActionArea>
            <div className={classes.cardFooter}>
                <div className={classes.chipContainer}>
                    {
                        itemTags.map(tag => {
                            if (tag != null) {
                                return (
                                    <Chip
                                        key={item.id + "-" + tag.id}
                                        label={tag?.name}
                                        variant={"outlined"}
                                        className={classes.cardChip}
                                    />
                                )
                            } else {
                                return null
                            }
                        })
                    }
                </div>
                <Typography variant={"subtitle1"} className={classes.footerText}>
                    {itemCategory != null ? (
                            <span>{itemCategory?.name}</span>)
                        : null}
                    <span className={classes.dateText}>{formatDate(editedDate)}</span>
                </Typography>
            </div>
            <CardActions disableSpacing className={`${classes.cardActionArea} card-actions`}>
                <IconButton aria-label="color" size={"small"} className={classes.cardIconBtn} onClick={() => {
                    props.onPinUnpin(item)
                }}>
                    {item.pinned ? (<PinFilledIcon fontSize={"small"}/>) : (<PinIcon fontSize={"small"}/>)}
                </IconButton>
                <IconButton aria-label="color" size={"small"} className={classes.cardIconBtn}
                            onClick={() => props.onColor(item)}>
                    <PaletteOutlined fontSize={"small"}/>
                </IconButton>
                <IconButton aria-label="archive" size={"small"}
                            className={classes.cardIconBtn}
                            onClick={() => props.onArchiveUnarchive(item)}>
                    {
                        item.archived ? <UnarchiveOutlined fontSize={"small"}/> : <ArchiveOutlined fontSize={"small"}/>
                    }
                </IconButton>
                <IconButton aria-label="tag" size={"small"} className={classes.cardIconBtn}
                            onClick={() => props.onTag(item, itemTags)}>
                    <LabelOutlined fontSize={"small"}/>
                </IconButton>
                <IconButton aria-label="categorize" size={"small"} className={classes.cardIconBtn}
                            onClick={() => props.onCategorize(item, itemCategory)}>
                    <CategoryOutlined fontSize={"small"}/>
                </IconButton>
                <IconButton aria-label="card-menu" className={`${classes.menuIcon} ${classes.cardIconBtn}`}
                            size={"small"} onClick={(e) => props.onMenuOpen(e.currentTarget, item)}>
                    <MoreVertIcon fontSize={"small"}/>
                </IconButton>
            </CardActions>
        </Card>
    );
}
