import React from "react";
import {createStyles, makeStyles, Theme, useTheme} from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import useAppState from "../../store/AppStateProvider";
import {Archive, Category, Dashboard, Delete, Label, Notifications} from "@material-ui/icons";
import {NavLink} from "react-router-dom"
import {Hidden} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";

export const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        drawer: {
            [theme.breakpoints.up('sm')]: {
                width: drawerWidth,
                flexShrink: 0,
                zIndex: 99
            },
        },
        menuButton: {
            marginRight: theme.spacing(2),
            [theme.breakpoints.up('sm')]: {
                display: 'none',
            },
        },
        toolbar: {
            ...theme.mixins.toolbar,
            marginTop: "1em",
        },
        drawerPaper: {
            width: drawerWidth,
        },
        sectionTitle: {
            marginLeft: "1.4em",
            color: theme.palette.text.secondary
        },
        navLink: {
            textDecoration: "none",
            color: theme.palette.text.primary
        },
        activeNavLink: {
            color: theme.palette.getContrastText(theme.palette.primary.light),
            backgroundColor: theme.palette.primary.light,
            display: "block"
        }
    }),
);

export default function SideDrawer(props: any) {

    const {drawerOpen, setDrawerOpen} = useAppState()
    const classes = useStyles();
    const theme = useTheme();

    const handleDrawerToggle = () => {
        setDrawerOpen(!drawerOpen);
    };

    const drawer = (
        <div>
            <div className={classes.toolbar}/>
            <Typography variant={"caption"} className={classes.sectionTitle}>
                Dashboard
            </Typography>
            <List>
                <NavLink to={"/"} className={classes.navLink} exact activeClassName={classes.activeNavLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Dashboard/>
                        </ListItemIcon>
                        <ListItemText primary={"Personal"}/>
                    </ListItem>
                </NavLink>
            </List>
            <Typography variant={"caption"} className={classes.sectionTitle}>
                Produce
            </Typography>
            <List>
                <NavLink to={"/reminders"} className={classes.navLink} exact activeClassName={classes.activeNavLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Notifications/>
                        </ListItemIcon>
                        <ListItemText primary={"Reminders"}/>
                    </ListItem>
                </NavLink>
                <NavLink to={"/archive"} className={classes.navLink} exact activeClassName={classes.activeNavLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Archive/>
                        </ListItemIcon>
                        <ListItemText primary={"Archive"}/>
                    </ListItem>
                </NavLink>
                <NavLink to={"/trash"} className={classes.navLink} exact activeClassName={classes.activeNavLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Delete/>
                        </ListItemIcon>
                        <ListItemText primary={"Trash"}/>
                    </ListItem>
                </NavLink>
            </List>
            <Typography variant={"caption"} className={classes.sectionTitle}>
                Organize
            </Typography>
            <List>
                <NavLink to={"/tags"} className={classes.navLink} exact activeClassName={classes.activeNavLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Label/>
                        </ListItemIcon>
                        <ListItemText primary={"Tags"}/>
                    </ListItem>
                </NavLink>
                <NavLink to={"/categories"} className={classes.navLink} exact activeClassName={classes.activeNavLink}>
                    <ListItem button>
                        <ListItemIcon>
                            <Category/>
                        </ListItemIcon>
                        <ListItemText primary={"Categories"}/>
                    </ListItem>
                </NavLink>
            </List>
            {/*    todo display user categories    */}
        </div>
    )

    return (
        <nav className={classes.drawer} aria-label="mailbox folders">
            {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
            <Hidden smUp implementation="css">
                <Drawer
                    variant="temporary"
                    anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                    open={drawerOpen}
                    onClose={handleDrawerToggle}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                    ModalProps={{
                        keepMounted: true, // Better open performance on mobile.
                    }}
                >
                    {drawer}
                </Drawer>
            </Hidden>
            <Hidden xsDown implementation="css">
                <Drawer
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                    variant="permanent"
                    open
                >
                    {drawer}
                </Drawer>
            </Hidden>
        </nav>
    );
}
