import React, {useState} from "react";

import Content from "../components/Content";
import {createStyles, Fab} from "@material-ui/core";
import {Notifications} from "@material-ui/icons";
import {ReminderDialog} from "../components/dialogs/ReminderDialog";
import {makeStyles} from "@material-ui/styles";
import {Theme} from "@material-ui/core/styles";
import RemindersList from "../components/reminders/RemindersList";
import EmptyList from "../components/EmptyList";

const useStyles = makeStyles((theme: Theme) => createStyles({
    reminderFab: {
        position: "fixed",
        bottom: theme.spacing(4),
        right: theme.spacing(4),
        background: theme.palette.primary.main
    }
}))

export default function Reminders() {

    const classes = useStyles()
    const [reminderDialog, setReminderDialog] = useState(false)

    return (
        <Content>
            <RemindersList
                EmptyListComponent={
                    <EmptyList/>
                }
            />
            <ReminderDialog isOpen={reminderDialog} onClose={() => setReminderDialog(false)}/>
            <Fab
                onClick={() => {
                    setReminderDialog(true)
                }}
                className={classes.reminderFab}
            >
                <Notifications/>
            </Fab>
        </Content>
    );
}

