import Dexie from "dexie";
import {SyncFile, SyncFileType} from "../SyncFile";
import {Note} from "../Note";
import {MindMap} from "../MindMap";
import {Tag} from "../Tag";
import {Category} from "../Category";
import {Reminder} from "../Reminder";

export const db = new Dexie("TimelineDB")

const timelineItem = "id,cloudId,createdDate,editedDate,deletedDate,deleted,"

db.version(1).stores({
    notes: timelineItem + "title,text,content,pinned,archived,categoryId,reminderId,colorIndex,tags,attachments,permanentlyDeleted",
    mindmaps: timelineItem + "title,text,content,pinned,archived,categoryId,colorIndex,tags,attachments,permanentlyDeleted",
    reminders: timelineItem + "description,isSticky,lastTimeFired,firedTimes,markedDone,repeat,intervalType,intervalNumber,weekDays,sameMonthDay,forever,untilDate,untilEvents",
    tags: timelineItem + "name",
    categories: timelineItem + "name",
    attachments: timelineItem + "fileCloudId,name,mimeType,type",
});

// @ts-ignore
db.notes.mapToClass(Note)
// @ts-ignore
db.mindmaps.mapToClass(MindMap)
// @ts-ignore
db.reminders.mapToClass(Reminder)
// @ts-ignore
db.categories.mapToClass(Category)
// @ts-ignore
db.tags.mapToClass(Tag)

// db.attachments.mapToClass() todo map attachment


export function deleteAllDatabases() {

    // @ts-ignore
    const dbs = [db.notes, db.mindmaps, db.reminders, db.categories, db.tags] //todo attachment db

    dbs.forEach((db) => {
        db.delete().then((e: any) => {
            console.log("Deleted Db", e)
        }).catch((e: any) => {
            console.log("Could Not Delete Db", e)
        })
    })
}

/**
 * Gets a file from database and returns it
 * @param file
 */
export function getFileFromDb(file: SyncFile) {
    try {
        switch (file.type) {
            case SyncFileType.Note:
                // @ts-ignore
                return db.notes.where("cloudId").equals(file.id).first()
            case SyncFileType.MindMap:
                // @ts-ignore
                return db.mindmaps.where("cloudId").equals(file.id).first()
            case SyncFileType.Reminder:
                // @ts-ignore
                return db.reminders.where("cloudId").equals(file.id).first()
            case SyncFileType.Category:
                // @ts-ignore
                return db.categories.where("cloudId").equals(file.id).first()
            case SyncFileType.Tag:
                // @ts-ignore
                return db.tags.where("cloudId").equals(file.id).first()
            case SyncFileType.AttachmentRecord:
                // @ts-ignore
                return db.attachments.where("cloudId").equals(file.id).first()
            default:
                console.warn("Unkown File Type Found")
                return null
        }
    } catch (e) {
        console.log(e)
        return null
    }
}

export function DeleteFileFromDb(file: SyncFile) {
    switch (file.type) {
        case SyncFileType.Note:
            // @ts-ignore
            db.notes.where("cloudId").delete().catch(e => {
                console.log(e)
            })
            break;
        case SyncFileType.MindMap:
            // @ts-ignore
            db.mindmaps.where("cloudId").delete().catch((e) => {
                console.log(e)
            })
            break;
        case SyncFileType.Reminder:
            // @ts-ignore
            db.reminders.where("cloudId").delete().catch((e) => {
                console.log(e)
            })
            break;
        case SyncFileType.Category:
            // @ts-ignore
            db.categories.where("cloudId").delete().catch((e) => {
                console.log(e)
            })
            break;
        case SyncFileType.Tag:
            // @ts-ignore
            db.tags.where("cloudId").delete().catch((e) => {
                console.log(e)
            })
            break;
        case SyncFileType.AttachmentRecord:
            // @ts-ignore
            db.attachments.where("cloudId").delete().catch((e) => {
                console.log(e)
            })
            break;
        default:
            console.warn("Unkown File Type Found")
    }
}
