import {db} from "./AppDatabase";

/**
 * Adds a attachment into database
 * @param item
 * @constructor
 */
export function AddAttachmentIntoDb(item: any) { //todo replace with Attachment Type
    // @ts-ignore
    db.attachments.add(item).catch(e => {
        console.warn("Error Adding Attachment To Db ")
        console.log(e)
        console.log(item)
    })
}

/**
 * Adds a attachment into database
 * @param item
 * @constructor
 */
export function UpdateAttachmentIntoDb(item: any) { //todo replace with Attachment Type
    // @ts-ignore
    db.attachments.put(item).catch(e => {
        console.warn("Error Adding Attachment To Db ")
        console.log(e)
        console.log(item)
    })
}