import {TimelineItem} from "./TimelineItem";
import {getWeekdays} from "../utils/ReminderHelper";

export enum RepeatInterval {
    Hour = "Hour",
    Day = "Day",
    Week = "Week",
    Month = "Month",
    Year = "Year"
}

export class Reminder implements TimelineItem {
    createdDate: number = Date.now();
    deleted: boolean = false;
    editedDate: number = Date.now();
    deletedDate: number | null = null;
    id: number | null = null;
    cloudId: string | null = null;
    name: string = "";
    time: number = Date.now();
    description: string = ""
    isSticky: boolean = false
    lastTimeFired: number = -1
    firedTimes: number = 0
    markedDone: boolean = false

    //Repeat Properties
    repeat: boolean = false
    intervalType: RepeatInterval = RepeatInterval.Day

    //for day
    intervalNumber: number = 1

    //for week
    weekDays: Array<string> = getWeekdays()

    //for month
    sameMonthDay: boolean = false

    forever: boolean = true
    untilDate: number | null = null
    untilEvents: number | null = null
}
