import {BaseSelectionBar} from "../commons/BaseSelectionBar";
import IconButton from "@material-ui/core/IconButton";
import {DeleteOutline} from "@material-ui/icons";
import {FunctionComponent} from "react";
import {Reminder} from "../../../model/Reminder";

interface Props {
    checked: Array<Reminder>,
    setChecked: (checked: Array<Reminder>) => void,
    onDelete: () => void,
}

export const ReminderSelectionBar: FunctionComponent<Props> = (props) => {

    return (
        <BaseSelectionBar
            title={props.checked.length.toString()}
            isVisible={props.checked.length > 0}
            onClose={() => props.setChecked([])}
        >
            <IconButton onClick={props.onDelete}>
                <DeleteOutline/>
            </IconButton>
        </BaseSelectionBar>
    )
}

ReminderSelectionBar.defaultProps = {}
