import React, {useEffect, useState} from 'react';
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import useStorage from "../../store/StorageProvider";
import {Check, ChevronRight, DeleteOutline, EditOutlined, LabelOutlined} from "@material-ui/icons";
import Content from "../components/Content";
import useLongPress from "../../utils/useLongPress";
import {AddCategoryIntoDb} from "../../model/database/CategoryDao";
import {formatDate} from "../../utils/DateTimeFormatter";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import {getNextId} from "../../utils/Helpers";
import {Icon, InputBase} from "@material-ui/core";
import {Category} from "../../model/Category";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        categoriesList: {
            width: '100%',
            backgroundColor: theme.palette.background.paper,
        },
    }),
);

export default function Categories() {
    const classes = useStyles();

    const [checked, setChecked] = React.useState([] as Array<number>);

    const handleToggle = (value: number) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    };

    const {categories, initCategories} = useStorage()

    useEffect(() => {
        initCategories()
    }, [initCategories])

    let toggleCheckItem = (id: number) => {
        let idIndex = checked.findIndex(cid => {
            return cid === id
        })
        if (idIndex < 0) {
            setChecked([...checked, id])
        } else {
            let checkedArr = [...checked]
            checkedArr.splice(idIndex, 1);
            setChecked(checkedArr)
        }
    }

    let longPressEvent = useLongPress({
        onLongPress: (e) => {
            // @ts-ignore
            let id = parseInt(e.currentTarget.getAttribute("catid").trim())
            if (id != null) {
                toggleCheckItem(id)
            }
        },
        onClick: (e) => {
            if (checked.length > 0) {
                // @ts-ignore
                let id = parseInt(e.currentTarget.getAttribute("catid").trim())
                if (id != null) {
                    toggleCheckItem(id)
                }
            } else {
                //todo opened category
            }
        }
    }, {delay: 600})

    return (
        <Content>
            <CategoryCreator/>
            <List className={classes.categoriesList}>
                {categories.map((category) => {
                    return (// @ts-ignore
                        <ListItem key={category.id} role={undefined} catid={category.id} dense
                                  button {...longPressEvent} >
                            {checked.length > 0 ? (
                                <ListItemIcon>
                                    <Checkbox
                                        edge="start"
                                        checked={checked.findIndex((categoryId) => {
                                            return categoryId === category.id
                                        }) > -1}
                                        tabIndex={-1}
                                        disableRipple
                                    />
                                </ListItemIcon>
                            ) : (
                                <ListItemIcon>
                                    <ChevronRight/>
                                </ListItemIcon>
                            )}
                            <ListItemText
                                primary={category.name}
                                secondary={formatDate(category.createdDate)}
                            />
                            <ListItemSecondaryAction>
                                <IconButton edge="end" aria-label="comments">
                                    <DeleteOutline/>
                                </IconButton>
                                <IconButton edge="end" aria-label="comments">
                                    <EditOutlined/>
                                </IconButton>
                                <IconButton edge="end" aria-label="comments">
                                    <MoreVertIcon/>
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    );
                })}
            </List>
        </Content>
    );
}


const useCreatorStyles = makeStyles((theme: Theme) =>
    createStyles({
        creatorRow: {
            display: "flex",
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center"
        },
        creatorIcon: {
            marginLeft: "0.8em"
        },
        categoryInput: {
            padding: "0.6em 1.5em"
        },
        creatorBtn: {
            marginRight: "1em"
        }
    }),
);


export function CategoryCreator() {

    const classes = useCreatorStyles()

    const {categories, initCategories} = useStorage()
    const [categoryName, setCategoryName] = useState("")

    const createCategory = () => {
        let newCategory = new Category(categoryName)
        newCategory.id = getNextId(categories)
        AddCategoryIntoDb(newCategory).then(() => {
            initCategories()
        }).then(() => {
            setCategoryName("")
        })
    }

    return (
        <div className={classes.creatorRow}>
            <Icon className={classes.creatorIcon}>
                <LabelOutlined/>
            </Icon>
            <InputBase
                fullWidth={true}
                placeholder={"Create Category"}
                className={classes.categoryInput}
                value={categoryName}
                onChange={(x: any) => {
                    setCategoryName(x.target.value)
                }}
            />
            <IconButton className={classes.creatorBtn} onClick={createCategory}>
                <Check/>
            </IconButton>
        </div>
    )
}
