import React from 'react';
import {AddTagIntoDb, UpdateTagIntoDb} from "../../model/database/TagDao";
import Content from "../components/Content";
import {Tag} from "../../model/Tag";
import {getNextId} from "../../utils/Helpers";
import TagCreator from "../components/tags/TagCreator";
import TagsList from "../components/tags/TagsList";
import EmptyList from "../components/EmptyList";


export default function Tags() {
    return (
        <Content>
            <TagCreator/>
            <TagsList
                EmptyListComponent={
                    <EmptyList/>
                }
            />
        </Content>
    );
}


export const createTag = async (tagName: string, tags: Array<Tag>) => {
    let newTag = new Tag(tagName)
    newTag.id = getNextId(tags)
    await AddTagIntoDb(newTag)
}

/**
* Updates Tag To Deleted Without Removing It From Database
* @param tag
* @param setTags
*/
export const deleteTag = async (tag: Tag) =>
{
    tag.deleted = true
    await UpdateTagIntoDb(tag)
}

