import {BaseSelectionBar} from "../commons/BaseSelectionBar";
import IconButton from "@material-ui/core/IconButton";
import {DeleteOutline} from "@material-ui/icons";

export function TagSelectionBar(props: { checked: Array<number>, setChecked: (checked: Array<number>) => void, onDelete: () => void }) {
    return (
        <BaseSelectionBar
            title={props.checked.length.toString()}
            isVisible={props.checked.length > 0}
            onClose={() => props.setChecked([])}
        >
            <IconButton onClick={props.onDelete}>
                <DeleteOutline/>
            </IconButton>
        </BaseSelectionBar>
    )
}
