import React, {useEffect, useState} from 'react';
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import {
    Button,
    Dialog,
    FormControl,
    IconButton,
    MenuItem,
    Radio,
    Select,
    TextField,
    Typography
} from "@material-ui/core";
import {Reminder, RepeatInterval} from "../../../model/Reminder";
import {getWeekdays} from "../../../utils/ReminderHelper";
import {DateTimePicker, MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {format} from "date-fns";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            display: "flex",
            flexDirection: "column",
        },
        formControl: {},
        formRow: {
            width: "100%",
        },
        formRowMargin: {
            width: "100%",
            marginTop: "1em"
        },
        marginContainer: {
            margin: "1em"
        },
        centerText: {
            textAlign: "center"
        },
        buttonsContainer: {
            display: "flex",
            flexFlow: "row no-wrap",
            justifyContent: "flex-end",
        },
        daysContainer: {
            display: "flex",
            flexDirection: "row",
            flexWrap: "wrap",
            justifyContent: "center",
            marginTop: "1em"
        },
        dayBtn: {
            width: "3em",
            height: "3em",
            borderRadius: "50%",
            padding: '0.4em'
        },
        dayItem: {
            width: "2em",
            height: "2em",
            lineHeight: '1.75em',
            textAlign: "center",
            background: "rgba(0,0,0,.15)",
            borderRadius: "50%",
            cursor: "pointer",
        },
        dayItemText: {
            fontSize: "16px",
        },
        selectedDayItem: {
            border: "1px solid " + theme.palette.text.secondary,
            background: theme.palette.primary.main,
        },
        monthContainer: {
            marginTop: "1em"
        },
        monthRow: {
            display: "flex",
            flexDirection: "row",
            alignItems: "center"
        },
        untilEvents: {
            margin: "1em"
        }
    }),
);

interface ReminderIntervalDialogProps {
    isOpen: boolean,
    onClose: () => void,
    reminder: Reminder,
}

export const ReminderIntervalDialog = (props: ReminderIntervalDialogProps) => {

    const classes = useStyles();
    let reminder = props.reminder
    const [intervalType, setIntervalType] = React.useState(reminder.intervalType);
    const [intervalNumber, setIntervalNumber] = useState(reminder.intervalNumber)
    const [until, setUntil] = useState("forever")
    const [reminderDays, setReminderDays] = useState(reminder.weekDays)
    const [sameDay, setSameDay] = useState(reminder.sameMonthDay)
    const [untilDateDialog, setUntilDateDialog] = useState(false)
    const [untilDate, setUntilDate] = useState(reminder.untilDate == null ? null : new Date(reminder.untilDate))
    const [untilEvents, setUntilEvents] = useState(reminder.untilEvents || 1)

    const initFromReminder = (reminder: Reminder) => {
        setIntervalType(reminder.intervalType)
        setIntervalNumber(reminder.intervalNumber)
        if (reminder.forever) {
            setUntil("forever")
        } else if (reminder.untilDate != null) {
            setUntil("date")
        } else if (reminder.untilEvents != null) {
            setUntil("events")
        }
        setReminderDays(reminder.weekDays)
        setSameDay(reminder.sameMonthDay)
        setUntilDate(reminder.untilDate == null ? null : new Date(reminder.untilDate))
    }

    useEffect(() => {
        initFromReminder(props.reminder)
    }, [props.reminder])

    return (
        <Dialog
            open={props.isOpen}
            onClose={props.onClose}
            maxWidth={"xs"}
            fullWidth={true}
        >
            <div className={classes.root}>
                <FormControl variant="filled" className={`${classes.formControl} ${classes.formRow}`}>
                    <Select
                        value={intervalType}
                        onChange={(e) => {
                            setIntervalType(e.target.value as RepeatInterval)
                        }}
                        renderValue={(value) => {
                            if (value === RepeatInterval.Day) {
                                return "Repeats daily"
                            } else if (value === RepeatInterval.Week) {
                                return "Repeats weekly"
                            } else if (value === RepeatInterval.Month) {
                                return "Repeats monthly"
                            } else if (value === RepeatInterval.Year) {
                                return "Repeats yearly"
                            }
                            return value as string
                        }}
                    >
                        <MenuItem value={RepeatInterval.Day}>Daily</MenuItem>
                        <MenuItem value={RepeatInterval.Week}>Weekly</MenuItem>
                        <MenuItem value={RepeatInterval.Month}>Monthly</MenuItem>
                        <MenuItem value={RepeatInterval.Year}>Yearly</MenuItem>
                    </Select>
                </FormControl>
                <div className={classes.marginContainer}>
                    <Typography>
                        Repeats every <TextField
                        inputProps={{className: classes.centerText}}
                        type={"number"}
                        value={intervalNumber}
                        onChange={(e) => {
                            setIntervalNumber(parseInt(e.target.value))
                            reminder.intervalNumber = parseInt(e.target.value)
                        }}
                    /> {intervalType}{intervalNumber > 1 ? "s" : null}
                    </Typography>
                    {
                        intervalType === RepeatInterval.Week ? (
                            <div className={classes.daysContainer}>
                                {getWeekdays().map(day =>
                                    (
                                        <IconButton
                                            className={classes.dayBtn}
                                            onClick={() => {
                                                let index = reminderDays.indexOf(day)
                                                if (index > -1) {
                                                    let newDays = [...reminderDays]
                                                    newDays.splice(index, 1)
                                                    setReminderDays(newDays)
                                                } else {
                                                    setReminderDays([...reminderDays, day])
                                                }
                                            }}>
                                            <div
                                                key={day}
                                                className={`${classes.dayItem} ${reminderDays.indexOf(day) > -1 ? classes.selectedDayItem : ""}`}>
                                                <span
                                                    className={classes.dayItemText}>{day[0].toUpperCase() + day.substring(1, 3)}</span>
                                            </div>
                                        </IconButton>
                                    ))
                                }
                            </div>
                        ) : intervalType === RepeatInterval.Month ? (
                            <div className={classes.monthContainer}>
                                <div className={classes.monthRow}>
                                    <Radio
                                        checked={sameDay}
                                        onClick={() => setSameDay(true)}
                                    />
                                    <Typography>
                                        Same day of the next month
                                    </Typography>
                                </div>
                                <div className={classes.monthRow}>
                                    <Radio
                                        checked={!sameDay}
                                        onClick={() => setSameDay(false)}
                                    />
                                    <Typography>
                                        Same date of the next month
                                    </Typography>
                                </div>
                            </div>
                        ) : null
                    }
                    <FormControl variant="outlined" className={`${classes.formControl} ${classes.formRowMargin}`}>
                        <Select
                            value={until}
                            onChange={(e) => {
                                let value = e.target.value as string
                                if (value === "date") {
                                    setUntilDateDialog(true)
                                } else if (value === "events") {
                                    //todo show events number
                                }
                                setUntil(value)
                            }}
                            renderValue={(value) => {
                                if (value === "forever") {
                                    return "Repeats forever"
                                } else if (value === "date" && untilDate != null) {
                                    return "Until " + format(untilDate, "p PPPP")
                                } else if (value === "events") {
                                    //todo return events value
                                    return "For " + untilEvents + " events"
                                }
                                return value as string
                            }}
                        >
                            <MenuItem value={"forever"}>Forever</MenuItem>
                            <MenuItem value={"date"}>Until Specified Date</MenuItem>
                            <MenuItem value={"events"}>For number of events</MenuItem>
                        </Select>
                    </FormControl>
                    {
                        until === "events" ? (
                            <TextField
                                type={"number"}
                                value={untilEvents}
                                className={classes.untilEvents}
                                onChange={(e) => setUntilEvents(parseInt(e.target.value))}
                            />
                        ) : null
                    }
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DateTimePicker
                            value={untilDate}
                            open={untilDateDialog}
                            onClose={() => setUntilDateDialog(false)}
                            disablePast={true}
                            onChange={(date) => {
                                if (date != null) {
                                    setUntilDate(date)
                                    reminder.time = date.getTime()
                                }
                            }}
                            TextFieldComponent={
                                () => null
                            }
                        />
                    </MuiPickersUtilsProvider>
                    <div className={`${classes.buttonsContainer} ${classes.formRowMargin}`}>
                        <Button variant={"outlined"} onClick={() => props.onClose()}>Cancel</Button>&nbsp;
                        <Button variant={"outlined"} onClick={() => {
                            reminder.repeat = true
                            reminder.intervalType = intervalType
                            reminder.intervalNumber = intervalNumber
                            reminder.weekDays = reminderDays
                            reminder.sameMonthDay = sameDay
                            if (until === "forever") {
                                reminder.forever = true
                            } else if (until === "date" && untilDate != null) {
                                reminder.untilDate = untilDate.getTime()
                            } else if (until === "events") {
                                reminder.untilEvents = untilEvents
                            }
                            props.onClose()
                        }}>Set</Button>
                    </div>
                </div>
            </div>
        </Dialog>
    );
}
