import React from "react";
import Content from "../components/Content";
import {MainList} from "../components/MainItem/MainList";
import CreateFab from "../components/commons/CreateFab";
import EmptyList from "../components/EmptyList";

export default function Main() {
    return (
        <Content>
            <MainList
                mainFilter={{
                    isDeleted: false,
                    isPermanentlyDeleted: false,
                    isArchived: false
                }}
                filters={[
                    {
                        name: "Pinned",
                        isPinned: true
                    },
                    {
                        name: "Others",
                        isPinned: false
                    }]}
                EmptyListComponent={
                    <EmptyList/>
                }
            />
            <CreateFab/>
        </Content>
    );
}
