import {SyncFile, SyncFileType} from "../model/SyncFile";
import {DeleteFileFromDb, getFileFromDb} from "../model/database/AppDatabase";
import {getFileFromCloud, getFilesList, uploadJsonToCloud} from "./DriveHelper";
import {Note} from "../model/Note";
import {MindMap} from "../model/MindMap";
import {Category} from "../model/Category";
import {Tag} from "../model/Tag";
import {AddNoteIntoDb, getAllNotes, UpdateNoteIntoDb} from "../model/database/NoteDao";
import {AddMindMapIntoDb, getAllMindMaps, UpdateMindMapIntoDb} from "../model/database/MindMapDao";
import {AddReminderIntoDb, getAllReminders, UpdateReminderIntoDb} from "../model/database/ReminderDao";
import {AddCategoryIntoDb, getAllCategories, UpdateCategoryIntoDb} from "../model/database/CategoryDao";
import {AddTagIntoDb, getAllTags, UpdateTagIntoDb} from "../model/database/TagDao";
import {AddAttachmentIntoDb, UpdateAttachmentIntoDb} from "../model/database/AttachmentDao";
import {Reminder} from "../model/Reminder";

export function resolveType(type: string): SyncFileType {
    switch (type) {
        case "Note":
            return SyncFileType.Note
        case "MindMap":
            return SyncFileType.MindMap
        case "Reminder":
            return SyncFileType.Reminder
        case "Category":
            return SyncFileType.Reminder
        case "Tag":
            return SyncFileType.Tag
        case "AttachmentRecord":
            return SyncFileType.AttachmentRecord
        case "Attachment":
            return SyncFileType.Attachment
        default:
            console.warn("Unknown file type found")
            return SyncFileType.Unknown
    }
}

export function convertItemToSyncFile(item: Note | MindMap | Reminder | Category | Tag): SyncFile { // todo attachment record sync file
    let itemType = item instanceof Note ? SyncFileType.Note :
        item instanceof MindMap ? SyncFileType.MindMap :
            item instanceof Reminder ? SyncFileType.Reminder :
                item instanceof Category ? SyncFileType.Category : SyncFileType.Tag
    // item instanceof Attachment ? SyncFileType.AttachmentRecord : ; //todo attachment type

    let itemName = (item instanceof Note || item instanceof MindMap) ? item.title :
        (item instanceof Reminder || item instanceof Category || item instanceof Tag) ? item.name : ""

    let isDeleted = (item instanceof Note || item instanceof MindMap) ? item.deleted && item.permanentlyDeleted : item.deleted

    return {
        createdDate: item.createdDate,
        deletedDate: item.deletedDate,
        editedDate: item.editedDate,
        id: item.cloudId,
        isDeleted: isDeleted,
        name: itemName,
        type: itemType
    }
}

/**
 * Starts Sync From Google Drive To IndexedDb
 */
export async function startDownloadSync() {
    try {
        const files = await getFilesList()
        files.forEach((file: SyncFile) => {
            getFileFromDb(file).then(async (resp: any) => {
                let isPresent = resp != null
                let isUpdated = (resp != null && resp.editedDate != null && resp.editedDate >= file.editedDate)
                if (!isPresent || !isUpdated) { //Note does not exist in db

                    //Downloading from cloud
                    let data = await getFileFromCloud(file)

                    //Saving Into Db
                    if (data != null && data) {

                        data.cloudId = file.id

                        switch (file.type) {
                            case SyncFileType.Note:
                                // @ts-ignore
                                if (!isPresent) {
                                    AddNoteIntoDb(data)
                                } else if (isPresent && !isUpdated) {
                                    if (file.isDeleted) {
                                        DeleteFileFromDb(file)
                                    } else {
                                        UpdateNoteIntoDb(data)
                                    }
                                }
                                break;
                            case SyncFileType.MindMap:
                                // @ts-ignore
                                if (!isPresent) {
                                    AddMindMapIntoDb(data)
                                } else if (isPresent && !isUpdated) {
                                    if (file.isDeleted) {
                                        DeleteFileFromDb(file)
                                    } else {
                                        UpdateMindMapIntoDb(data)
                                    }
                                }
                                break;
                            case SyncFileType.Reminder:
                                // @ts-ignore
                                if (!isPresent) {
                                    AddReminderIntoDb(data)
                                } else if (isPresent && !isUpdated) {
                                    if (file.isDeleted) {
                                        DeleteFileFromDb(file)
                                    } else {
                                        UpdateReminderIntoDb(data)
                                    }
                                }
                                break;
                            case SyncFileType.Category:
                                // @ts-ignore
                                if (!isPresent) {
                                    AddCategoryIntoDb(data)
                                } else if (isPresent && !isUpdated) {
                                    if (file.isDeleted) {
                                        DeleteFileFromDb(file)
                                    } else {
                                        UpdateCategoryIntoDb(data)
                                    }
                                }
                                break;
                            case SyncFileType.Tag:
                                // @ts-ignore
                                if (!isPresent) {
                                    AddTagIntoDb(data)
                                } else if (isPresent && !isUpdated) {
                                    if (file.isDeleted) {
                                        DeleteFileFromDb(file)
                                    } else {
                                        UpdateTagIntoDb(data)
                                    }
                                }
                                break;
                            case SyncFileType.AttachmentRecord:
                                // @ts-ignore
                                if (!isPresent) {
                                    AddAttachmentIntoDb(data)
                                } else if (isPresent && !isUpdated) {
                                    if (file.isDeleted) {
                                        DeleteFileFromDb(file)
                                    } else {
                                        UpdateAttachmentIntoDb(data)
                                    }
                                }
                                break;
                            default:
                                console.warn("Unkown File Type Found")
                        }
                    } else {
                        console.warn("Requested file data invalid", resp, data)
                    }
                } else {
                    console.log("found updated record in db")
                }
            })
        })
    } catch (e) {
        console.log(e)
    }
}

export async function startUploadSync() {
    let filesList = await getFilesList()
    let notes = await getAllNotes()
    let mindmaps = await getAllMindMaps()
    let reminders = await getAllReminders()
    let categories = await getAllCategories()
    let tags = await getAllTags()

    await uploadItems(filesList, notes)
    await uploadItems(filesList, mindmaps)
    await uploadItems(filesList, reminders)
    await uploadItems(filesList, categories)
    await uploadItems(filesList, tags)

}

//Upload Sync Helper Functions
async function uploadItems(filesList: Array<SyncFile>, items: Array<Note | MindMap | Reminder | Category | Tag>) { //todo save attachment uploaded
    await items.forEach(item => {
        if (item.cloudId === "" || item.cloudId == null) {
            uploadJsonToCloud(convertItemToSyncFile(item), JSON.stringify(item)).then(itemId => {
                console.log("I found")
                console.log(itemId)
            })
        } else {
            let file = filesList.find(file => file.id === item.cloudId)
            if (file != null) {
                if (item.editedDate > file.editedDate) {
                    uploadJsonToCloud(convertItemToSyncFile(item), JSON.stringify(item)).then(itemId => {
                        console.log("YO")
                        console.log(itemId)
                    })
                }
            } else {
                console.warn("cloudId missing in cloud")
            }
        }
    })
}
