import React, {useEffect, useState} from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {Tag} from "../../../model/Tag";
import {AddTagIntoDb, getAllTags, UpdateTagIntoDb} from "../../../model/database/TagDao";
import {getNextId} from "../../../utils/Helpers";
import useStorage from "../../../store/StorageProvider";

export default function TagDialog(props: { tag: Tag, open: boolean, setOpen: (open: boolean) => void }) {

    const {tags, initTags} = useStorage()
    const [tagName, setTagName] = useState(props.tag.name)

    useEffect(() => {
        setTagName(props.tag.name)
    }, [props.tag])

    return (
        <Dialog open={props.open} onClose={() => props.setOpen(false)} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">
                {
                    props.tag.id == null || props.tag.id === -1 ? "Create Tag" : "Update Tag"
                }
            </DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    label="Tag Name"
                    type="text"
                    value={tagName}
                    onChange={(e) => {
                        setTagName(e.currentTarget.value)
                    }}
                    fullWidth
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={() => props.setOpen(false)} color="primary">
                    Cancel
                </Button>
                <Button color="primary" onClick={() => {
                    props.tag.name = tagName
                    props.setOpen(false);
                    if (props.tag.id == null || props.tag.id === -1) {
                        props.tag.id = getNextId(tags)
                        AddTagIntoDb(props.tag)
                    } else {
                        UpdateTagIntoDb(props.tag)
                    }
                    getAllTags().then((tags) => {
                        initTags()
                    })
                }}>
                    {
                        props.tag.id == null || props.tag.id === -1 ? "Create" : "Update"
                    }
                </Button>
            </DialogActions>
        </Dialog>
    );
}
