import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import {Tag} from "../../../model/Tag";
import useStorage from "../../../store/StorageProvider";
import {Menu, MenuItem, Typography} from "@material-ui/core";
import React from "react";
import {deleteTag} from "../../screens/Tags";

interface TagItemMenuProps {
    onShowTagDialog: () => void,
    onItemSelect: () => void,
    tag: Tag,
    anchorEl: any,
    onMenuClose: () => void
}

const useTagMenuStyles = makeStyles((theme: Theme) =>
    createStyles({
        menuItem: {},
    }),
)

export default function TagItemMenu(props: TagItemMenuProps) {

    const classes = useTagMenuStyles()

    const {initTags} = useStorage()

    return (
        <Menu
            open={Boolean(props.anchorEl)}
            onClose={props.onMenuClose}
            anchorEl={props.anchorEl}
        >
            <MenuItem onClick={() => {
                props.onItemSelect()
                props.onMenuClose()
            }}>
                <Typography variant={"inherit"} className={classes.menuItem}>
                    Select
                </Typography>
            </MenuItem>
            <MenuItem onClick={() => {
                props.onShowTagDialog()
                props.onMenuClose()
            }}>
                <Typography variant={"inherit"} className={classes.menuItem}>
                    Edit
                </Typography>
            </MenuItem>
            <MenuItem onClick={() => {
                deleteTag(props.tag).then(() => {
                    initTags()
                })
                props.onMenuClose()
            }}>
                <Typography variant={"inherit"} className={classes.menuItem}>
                    Delete
                </Typography>
            </MenuItem>
        </Menu>
    )
}
