import {BaseSelectionBar} from "../commons/BaseSelectionBar";
import IconButton from "@material-ui/core/IconButton";
import {ArchiveOutlined, CategoryOutlined, DeleteOutline, LabelOutlined, UnarchiveOutlined} from "@material-ui/icons";
import PinIcon from "../../icons/PinIcon";
import {Note} from "../../../model/Note";
import PinFilledIcon from "../../icons/PinFilledIcon";
import {FunctionComponent} from "react";
import {MindMap} from "../../../model/MindMap";

interface Props {
    checked: Array<Note | MindMap>,
    setChecked: (checked: Array<Note | MindMap>) => void,
    showPinUnpin?: boolean,
    showArchiveUnArchive?: boolean,
    showTag?: boolean,
    showCategory?: boolean,
    onPinUnPin: () => void,
    onArchiveUnArchive: () => void,
    onDelete: () => void,
    onTag: () => void,
    onCategorize: () => void,
}

export const MainSelectionBar: FunctionComponent<Props> = (props) => {

    return (
        <BaseSelectionBar
            title={props.checked.length.toString()}
            isVisible={props.checked.length > 0}
            onClose={() => props.setChecked([])}
        >

            {props.showPinUnpin ? (
                <IconButton onClick={props.onPinUnPin}>
                    {
                        props.checked.filter(item => item.pinned).length > 0 ? (<PinFilledIcon/>) : (<PinIcon/>)
                    }
                </IconButton>
            ) : null}
            {props.showArchiveUnArchive ? (
                <IconButton onClick={props.onArchiveUnArchive}>
                    {
                        props.checked.filter(item => item.archived).length > 0 ? (<UnarchiveOutlined/>) : (
                            <ArchiveOutlined/>)
                    }
                </IconButton>
            ) : null}
            {props.showTag ? (
                <IconButton onClick={props.onTag}>
                    <LabelOutlined/>
                </IconButton>
            ) : null}
            {props.showCategory ? (
                <IconButton onClick={props.onCategorize}>
                    <CategoryOutlined/>
                </IconButton>
            ) : null}
            <IconButton onClick={props.onDelete}>
                <DeleteOutline/>
            </IconButton>
        </BaseSelectionBar>
    )
}

MainSelectionBar.defaultProps = {
    showPinUnpin: true,
    showArchiveUnArchive: true,
    showTag: true,
    showCategory: true
}
