import {Tag} from "../../../model/Tag";
import React from "react";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Checkbox from "@material-ui/core/Checkbox";
import {LabelOutlined} from "@material-ui/icons";
import ListItemText from "@material-ui/core/ListItemText";
import {formatDate} from "../../../utils/DateTimeFormatter";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import IconButton from "@material-ui/core/IconButton";
import MoreVertIcon from "@material-ui/icons/MoreVert";

interface TagsListItemProps {
    isSelectionMode: boolean,
    isSelected: boolean,
    tag: Tag,
    onShowTagDialog: () => void,
    onShowTagMenu: (anchor: any) => void
}


export default function TagsListItem(props: TagsListItemProps) {

    return (
        <React.Fragment>
            {props.isSelectionMode ? (
                <ListItemIcon>
                    <Checkbox
                        edge="start"
                        checked={props.isSelected}
                        tabIndex={-1}
                        disableRipple
                    />
                </ListItemIcon>
            ) : (
                <ListItemIcon>
                    <LabelOutlined/>
                </ListItemIcon>
            )}
            <ListItemText
                primary={props.tag.name}
                secondary={formatDate(props.tag.createdDate)}
            />
            <ListItemSecondaryAction style={{marginRight: "1em"}}>
                <IconButton edge="end" aria-label="menu" onClick={(e) => props.onShowTagMenu(e.currentTarget)}>
                    <MoreVertIcon/>
                </IconButton>
            </ListItemSecondaryAction>
        </React.Fragment>
    )
}
