import React, {useEffect, useState} from "react";
import {Link, useParams} from "react-router-dom"
import {getNoteById, UpdateNoteIntoDb} from "../../../model/database/NoteDao";
import {Note} from "../../../model/Note";
import RichMarkdownEditor from "rich-markdown-editor";
import Content from "../../components/Content";
import {createStyles, IconButton, InputBase} from "@material-ui/core";
import {ArrowBack} from "@material-ui/icons";
import "./NoteEditor.scss"
import useStorage from "../../../store/StorageProvider";
import {getNextId} from "../../../utils/Helpers";
import useAppState from "../../../store/AppStateProvider";
import {ThemeType} from "../../Themes";
import {editorDark, editorLight} from "./EditorTheme";
import {startUploadSync} from "../../../utils/SyncHelper";
import {makeStyles} from "@material-ui/styles";
import {Theme} from "@material-ui/core/styles";

const useStyles = makeStyles((theme: Theme) => createStyles({
    editorContainer: {},
    noteEditor: {
        width: "100%",
        padding: "1rem 1rem 1rem 2rem",
    },
    noteTitle: {
        width: "100%",
        border: 0,
        outline: 0,
        padding: "1rem 1rem 1rem 2rem",
        fontSize: theme.typography.h3.fontSize,
        fontFamily: theme.typography.h4.fontFamily
    }
}))

export default function NoteEditor() {

    // @ts-ignore
    const {id} = useParams()
    const classes = useStyles()

    let [note, setNote] = useState<Note>(new Note())
    let [noteTitle, setNoteTitle] = useState(note.title)

    let {notes} = useStorage()

    useEffect(() => {
        if (id != null) {
            try {
                getNoteById(parseInt(id)).then(n => {
                    if (n != null) {
                        setNote(n)
                        setNoteTitle(n.title)
                    }
                })
            } catch (e) {
                console.log(e)
            }
        } else {
            note.id = getNextId(notes)
        }
    }, [id, notes]) //do not add note as a dependency

    const {themeType} = useAppState()

    return (
        <Content>
            <div className={`${classes.editorContainer}`}>
                <Link to="/" onClick={() => {
                    note.title = noteTitle
                    note.editedDate = Date.now() //todo check if note is modified
                    UpdateNoteIntoDb(note).then(() => {
                        startUploadSync().then(r => console.log(r))
                    })
                }}>
                    <IconButton>
                        <ArrowBack/>
                    </IconButton>
                </Link>
                <InputBase
                    type={"text"}
                    value={noteTitle}
                    className={classes.noteTitle}
                    onChange={(e) => {
                        console.log(e.target.value)
                        setNoteTitle(e.target.value)
                    }}
                    placeholder={"Title"}
                />
                <RichMarkdownEditor
                    className={`${classes.noteEditor}`}
                    onChange={(getValue) => {
                        note.text = getValue()
                    }}
                    value={note.text}
                    placeholder={"Note"}
                    theme={themeType === ThemeType.Dark ? editorDark : editorLight}
                />
            </div>
        </Content>
    )
}
