import {MindMap} from "../MindMap";
import {db} from "./AppDatabase";

/**
 * Gets All Notes From Database
 */
export async function getAllMindMaps() {
    // @ts-ignore
    return await db.mindmaps.toArray()
}

/**
 * Adds a mindmap into database
 * @param item
 * @constructor
 */
export async function AddMindMapIntoDb(item: MindMap) {
    // @ts-ignore
    await db.mindmaps.add(item).catch(e => {
        console.warn("Error Adding Mindmap To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Adds a mindmap into database
 * @param item
 * @constructor
 */
export async function UpdateMindMapIntoDb(item: MindMap) {
    // @ts-ignore
    await db.mindmaps.put(item).catch(e => {
        console.warn("Error Adding Mindmap To Db")
        console.log(e)
        console.log(item)
    })
}