import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import TagItemMenu from "./TagItemMenu";
import TagDialog from "../dialogs/TagDialog";
import {TagSelectionBar} from "./TagSelectionBar";
import React, {useEffect, useState} from "react";
import {deleteTag} from "../../screens/Tags";
import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import useStorage from "../../../store/StorageProvider";
import useLongPress from "../../../utils/useLongPress";
import TagsListItem from "./TagsListItem";
import {Tag} from "../../../model/Tag";


interface TagsListProps {
    EmptyListComponent: React.ReactNode
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        tagsList: {
            width: '100%',
            backgroundColor: theme.palette.background.paper,
        },
    }),
);

export default function TagsList(props: TagsListProps) {


    const classes = useStyles();

    const [checked, setChecked] = React.useState([] as Array<number>);

    const {tags, initTags} = useStorage()

    const [tagDialogOpen, setTagDialogOpen] = useState(false)
    const [currentTag, setCurrentTag] = useState(new Tag(""))
    const [tagMenuAnchor, setTagMenuAnchor] = useState<any | null>()

    useEffect(() => {
        initTags()
    }, [initTags])

    const toggleCheckItem = (id: number) => {
        let idIndex = checked.indexOf(id)
        let newChecked = [...checked]

        if (idIndex < 0) {
            newChecked.push(id)
        } else {
            newChecked.splice(idIndex, 1);
        }

        setChecked(newChecked)
    }

    let longPressEvent = useLongPress({
        onLongPress: (e) => {
            // @ts-ignore
            let id = parseInt(e.currentTarget.getAttribute("tagid").trim())
            if (id != null) {
                toggleCheckItem(id)
            }
        },
        onClick: (e) => {
            if (checked.length > 0) {
                // @ts-ignore
                let id = parseInt(e.currentTarget.getAttribute("tagid").trim())
                if (id != null) {
                    toggleCheckItem(id)
                }
            } else {
                //todo opened tag
            }
        }
    }, {delay: 600})

    const {EmptyListComponent} = props

    return (
        tags.length > 0 ? (
            <List className={classes.tagsList}>
                {tags.filter(tag => !tag.deleted).map((tag) => {
                    return (// @ts-ignore
                        <ListItem key={tag.id} tagid={tag.id} dense button {...longPressEvent}>
                            <TagsListItem
                                isSelectionMode={checked.length > 0}
                                isSelected={checked.findIndex((tagId) => {
                                    return tagId === tag.id
                                }) > -1}
                                tag={tag}
                                onShowTagDialog={() => {
                                    setCurrentTag(tag)
                                    setTagDialogOpen(true)
                                }}
                                onShowTagMenu={(anchorEl: any) => {
                                    setCurrentTag(tag)
                                    setTagMenuAnchor(anchorEl)
                                }}
                            />
                        </ListItem>
                    );
                })}
                <TagItemMenu
                    onMenuClose={() => setTagMenuAnchor(null)}
                    anchorEl={tagMenuAnchor}
                    tag={currentTag}
                    onShowTagDialog={() => setTagDialogOpen(true)}
                    onItemSelect={() => {
                        if (currentTag.id != null) {
                            toggleCheckItem(currentTag.id)
                        }
                    }}
                />
                <TagDialog
                    tag={currentTag}
                    open={tagDialogOpen}
                    setOpen={setTagDialogOpen}
                />
                <TagSelectionBar
                    checked={checked}
                    setChecked={setChecked}
                    onDelete={() => {
                        checked.forEach(checkedId => {
                            let tag = tags.find(tag => tag.id === checkedId)
                            if (tag != null) {
                                deleteTag(tag).then(() => initTags())
                            }
                        })
                        setChecked([])
                    }}
                />
            </List>
        ) : (
            <React.Fragment>
                {EmptyListComponent}
            </React.Fragment>
        )
    )
}
