import React from "react";
import {ThemeType} from "../ui/Themes";

let defaultState = {
    themeType: ThemeType.Light,
    setThemeType: (type: ThemeType) => {

    },
    drawerOpen: false,
    setDrawerOpen: (open: boolean) => {
    },
    settingsOpen: false,
    setSettingsOpen: (open: boolean) => {

    },
    accountOpen: false,
    setAccountOpen: (open: boolean) => {

    }
};

const AppStateContext = React.createContext(defaultState);

export function AppStateProvider(props: { state: typeof defaultState; children: any }) {
    return (
        <AppStateContext.Provider value={props.state}>
            {props.children}
            <style>
                {props.state.themeType === ThemeType.Dark ? (
                    `:root {
                            color-scheme: dark;
                    }`) : null
                }
            </style>
        </AppStateContext.Provider>
    );
}

export default function useAppState() {
    let context = React.useContext(AppStateContext);
    if (context == null) {
        throw new Error("Storage Context outside provider");
    }
    return context;
}
