import React, {useEffect, useState} from 'react';
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import {Dialog, IconButton} from "@material-ui/core";
import useAppState from "../../../store/AppStateProvider";
import {ThemeType} from "../../Themes";
import {darkColorsList, lightColorsList} from "../../../utils/Helpers";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        colorRow: {
            display: "flex",
            flexDirection: "row",
            flexWrap: "wrap",
            justifyContent: "center",
            padding: "1rem"
        },
        colorNode: {
            width: "3rem",
            height: "3rem",
            borderRadius: "50%",
            margin: "0.4rem",
            cursor: "pointer"
        },
        activeColor: {
            border: "2px solid " + theme.palette.text.primary
        }
    }),
);

interface ColorDialogProps {
    isOpen: boolean,
    onClose: () => void,
    activeColorIndex: number,
    onSelectionChange: (colorIndex: number, color: string) => void
}

export const ColorDialog = (props: ColorDialogProps) => {

    const classes = useStyles();

    const {themeType} = useAppState()

    const [colorIndex, setColorIndex] = useState(props.activeColorIndex)

    useEffect(() => {
        setColorIndex(props.activeColorIndex)
    }, [props.activeColorIndex])

    let colorsList
    if (themeType === ThemeType.Light) {
        colorsList = lightColorsList
    } else {
        colorsList = darkColorsList
    }

    return (
        <Dialog
            open={props.isOpen}
            onClose={props.onClose}
            maxWidth={"xs"}
            fullWidth={false}
        >
            <div className={classes.colorRow}>
                {colorsList.map((color, index) => {
                    return (
                        <IconButton
                            key={color}
                            className={`${classes.colorNode} ${index === colorIndex ? classes.activeColor : ""}`}
                            style={{background: color}}
                            onClick={() => {
                                setColorIndex(index)
                                props.onSelectionChange(index, color)
                            }}
                        >
                        </IconButton>
                    )
                })}
            </div>
        </Dialog>
    );
}
