import React from "react";

export type User = {
    photoURL: string,
    displayName: string,
    email: string
}

export type UserProps = {
    user: User | null,
    setUser: (user: User | null) => void
}

const defaultUser = {
    user: null,
    setUser: (user: User | null) => {
    },
};

export const UserContext = React.createContext<UserProps>(defaultUser);

export function UserProvider(props: { user: UserProps, children: any }) {
    return (
        <UserContext.Provider value={props.user}>
            {props.children}
        </UserContext.Provider>
    );
}

export default function useUser() {
    let context = React.useContext(UserContext);
    if (context == null) {
        throw new Error("User Context outside provider");
    }
    return context;
}
