import {Reminder, RepeatInterval} from "../model/Reminder";
import {format} from "date-fns";

export function getWeekdays() {
    return [
        "monday",
        "tuesday",
        "wednesday",
        "thursday",
        "friday",
        "saturday",
        "sunday"
    ]
}

function getNextRepeatTime(reminder: Reminder): number {
    let reminderTime = reminder.time
    let reminderDate = new Date(reminder.time)
    if (reminder.repeat) {
        switch (reminder.intervalType) {
            case RepeatInterval.Hour:
                reminderTime = reminderDate.setHours(reminderDate.getHours() + reminder.intervalNumber)
                break;
            case RepeatInterval.Day:
                reminderTime = reminderDate.setDate(reminderDate.getDate() + reminder.intervalNumber)
                break;
            case RepeatInterval.Week:
                reminderTime = reminderDate.setDate(reminderDate.getDate() + (reminder.intervalNumber * 7))
                break;
            case RepeatInterval.Month:
                reminderTime = reminderDate.setMonth(reminderDate.getMonth() + reminder.intervalNumber)
                break;
            case RepeatInterval.Year:
                reminderTime = reminderDate.setFullYear(reminderDate.getFullYear() + reminder.intervalNumber)
                break;
        }
    }
    return reminderTime
}

/**
 * Return calculated next ringing time of reminder from its interval/repeat properties
 * @param reminder
 */
export function getNextRingingTime(reminder: Reminder): number {
    if (reminder.repeat) {
        if (reminder.lastTimeFired > 0 && reminder.time < Date.now()) {
            let nextTime = reminder.time
            if (reminder.forever) {
                nextTime = getNextRepeatTime(reminder)
            } else if (reminder.untilDate != null && reminder.untilDate!! > Date.now()) {
                nextTime = getNextRepeatTime(reminder)
            } else if (reminder.untilEvents != null && reminder.firedTimes > reminder.untilEvents!!) {
                nextTime = getNextRepeatTime(reminder)
            }
            return nextTime
        } else {
            return reminder.time
        }
    } else {
        return reminder.time
    }
}

/**
 * Sort reminder list on the basis of reminders next firing time
 */
export function sortRemindersList(reminders: Array<Reminder>): Array<Reminder> {
    let start = 0
    let end = reminders.length

    if (start > end) {
        for (let i = start; i < end; i++) {
            if (getNextRingingTime(reminders[start]) < getNextRingingTime(reminders[end - 1])) {
                let s = reminders[start]
                reminders[start] = reminders[end]
                reminders[end] = s
            }
            start++
            end--
        }
    }

    return reminders
}

/**
 * Returns reminder date time string , eg : Rings tomorrow 6 AM
 * @param reminder
 */
export function getReminderDateTimeString(reminder: Reminder): string {
    return format(new Date(getNextRingingTime(reminder)), "p PP")
}

/**
 * Returns repeat string , eg : Repeats every 6 days
 * @param reminder
 */
export function getReminderRepeatString(reminder: Reminder): string {
    if (reminder.repeat) {
        let str = "repeats every "
        if (reminder.intervalNumber > 1) {
            str += reminder.intervalNumber + " " + reminder.intervalType.toLowerCase() + "s "
        } else {
            str += reminder.intervalType.toLowerCase() + " "
        }
        if (reminder.intervalType === RepeatInterval.Week) {
            str += "on "
            str += reminder.weekDays.map(day => day[0].toUpperCase() + day.substring(1, 3)).join(',') + " "
        }

        if (reminder.intervalType === RepeatInterval.Month) {
            if (reminder.sameMonthDay) {
                str += "on the same day "
            } else {
                str += "on the same date "
            }
        }

        if (!reminder.forever) {
            if (reminder.untilDate != null) {
                str += "until " + format(reminder.untilDate, "p PP") + " "
            } else if (reminder.untilEvents != null) {
                str += "for " + reminder.untilEvents.toString() + " times "
            }
        }

        return str
    } else {
        return "Does not repeat"
    }
}
