import {Reminder} from "../../../model/Reminder";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Checkbox from "@material-ui/core/Checkbox";
import {NotificationsOutlined} from "@material-ui/icons";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import IconButton from "@material-ui/core/IconButton";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import React from "react";
import {getReminderDateTimeString, getReminderRepeatString} from "../../../utils/ReminderHelper";
import {createStyles, Theme} from "@material-ui/core/styles";
import {makeStyles} from "@material-ui/core";

interface RemindersListItemProps {
    reminder: Reminder,
    isSelectionMode: boolean,
    isSelected: boolean,
    onShowMenu: (anchor: any) => void
}

const useStyles = makeStyles((theme: Theme) => createStyles({
    reminderText: {
        display: "flex",
        flexDirection: "column"
    }
}))

export default function RemindersListItem(props: RemindersListItemProps) {

    const classes = useStyles()
    let reminder = props.reminder

    return (
        <React.Fragment>
            {props.isSelectionMode ? (
                <ListItemIcon>
                    <Checkbox
                        edge="start"
                        checked={props.isSelected}
                        tabIndex={-1}
                        disableRipple
                    />
                </ListItemIcon>
            ) : (
                <ListItemIcon>
                    <NotificationsOutlined/>
                </ListItemIcon>
            )}
            <div className={classes.reminderText}>
                <ListItemText
                    primary={reminder.name}
                    secondary={reminder.description}
                />
                <ListItemText
                    primary={getReminderDateTimeString(reminder)}
                    secondary={getReminderRepeatString(reminder)}
                />
            </div>
            <ListItemSecondaryAction style={{marginRight: "1em"}}>
                <IconButton edge="end" aria-label="menu" onClick={(e) => {
                    props.onShowMenu(e.currentTarget)
                }}>
                    <MoreVertIcon/>
                </IconButton>
            </ListItemSecondaryAction>
        </React.Fragment>
    )
}
