import {createStyles, makeStyles, Theme} from "@material-ui/core/styles";
import useStorage from "../../../store/StorageProvider";
import {Menu, MenuItem, Typography} from "@material-ui/core";
import React from "react";
import {Reminder} from "../../../model/Reminder";
import {removeReminder} from "./RemindersList";

interface ReminderItemMenuProps {
    onShowReminderDialog: () => void,
    onItemSelect: () => void,
    reminder: Reminder,
    anchorEl: any,
    onMenuClose: () => void
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        menuItem: {},
    }),
)

export default function ReminderItemMenu(props: ReminderItemMenuProps) {

    const classes = useStyles()

    const {initReminders} = useStorage()

    return (
        <Menu
            open={Boolean(props.anchorEl)}
            onClose={props.onMenuClose}
            anchorEl={props.anchorEl}
        >
            <MenuItem onClick={() => {
                props.onItemSelect()
                props.onMenuClose()
            }}>
                <Typography variant={"inherit"} className={classes.menuItem}>
                    Select
                </Typography>
            </MenuItem>
            <MenuItem onClick={() => {
                props.onShowReminderDialog()
                props.onMenuClose()
            }}>
                <Typography variant={"inherit"} className={classes.menuItem}>
                    Edit
                </Typography>
            </MenuItem>
            <MenuItem onClick={() => {
                removeReminder(props.reminder).then(() => {
                    initReminders()
                })
                props.onMenuClose()
            }}>
                <Typography variant={"inherit"} className={classes.menuItem}>
                    Delete
                </Typography>
            </MenuItem>
        </Menu>
    )
}
