import React, {FunctionComponent, useEffect} from 'react';
import {createStyles, makeStyles, Theme} from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import {Dialog} from "@material-ui/core";
import useStorage from "../../../store/StorageProvider";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import {Tag} from "../../../model/Tag";
import TagCreator from "../tags/TagCreator";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            width: '100%',
            maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
    }),
);

interface TagSelectionDialogProps {
    isOpen: boolean,
    onClose: (tags: Array<Tag>) => void,
    selectedTags: Array<Tag>,
    onSelectionChange?: (tags: Array<Tag>) => void,
}

export const TagSelectionDialog: FunctionComponent<TagSelectionDialogProps> = (
    {
        isOpen,
        onClose,
        selectedTags,
        onSelectionChange
    }) => {

    const classes = useStyles();
    const [checked, setChecked] = React.useState(selectedTags);

    useEffect(() => {
        setChecked(selectedTags)
    }, [selectedTags])

    const {tags} = useStorage()

    const handleToggle = (tag: Tag) => () => {
        const currentIndex = checked.findIndex(t => t.id === tag.id);
        const newChecked = [...checked];

        if (currentIndex < 0) {
            newChecked.push(tag);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
        onSelectionChange!(newChecked)
    };

    return (
        <Dialog
            open={isOpen}
            onClose={() => onClose(checked)}
            maxWidth={"md"}
            fullWidth={false}
        >
            <List className={classes.root}>
                <TagCreator/>
                {tags.map((tag) => {

                    const labelId = `checkbox-list-label-${tag.id}`;

                    return (
                        <ListItem key={tag.id} role={undefined} dense button onClick={handleToggle(tag)}>
                            <ListItemIcon>
                                <Checkbox
                                    edge="start"
                                    checked={checked.findIndex(t => t.id === tag.id) > -1}
                                    tabIndex={-1}
                                    disableRipple
                                    inputProps={{'aria-labelledby': labelId}}
                                />
                            </ListItemIcon>
                            <ListItemText id={labelId} primary={tag.name}/>
                            <ListItemSecondaryAction>
                                <IconButton edge="end" aria-label="comments">
                                    <MoreVertIcon/>
                                </IconButton>
                            </ListItemSecondaryAction>
                        </ListItem>
                    );
                })}
            </List>
        </Dialog>
    );
}

TagSelectionDialog.defaultProps = {
    onSelectionChange: () => {

    }
}
