import {db} from "./AppDatabase";
import {Reminder} from "../Reminder";

/**
 * Gets All Reminders From Database
 */
export async function getAllReminders() {
    // @ts-ignore
    return await db.reminders.toArray()
}

/**
 * Adds a reminder into database
 * @param item
 * @constructor
 */
export async function AddReminderIntoDb(item: Reminder) {
    // @ts-ignore
    await db.reminders.add(item).catch(e => {
        console.warn("Error Adding Reminder To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Adds a reminder into database
 * @param item
 * @constructor
 */
export async function UpdateReminderIntoDb(item: Reminder) {
    // @ts-ignore
    await db.reminders.put(item).catch(e => {
        console.warn("Error Adding Reminder To Db")
        console.log(e)
        console.log(item)
    })
}