import {TimelineItem} from "./TimelineItem";

export class Tag implements TimelineItem {
    createdDate: number = Date.now();
    deleted: boolean = false;
    deletedDate: number | null = null;
    editedDate: number = Date.now();
    id: number | null = null;
    name: string = "";
    cloudId: string | null = null;

    constructor(name: string) {
        this.name = name
    }
}
