import {TimelineItem} from "./TimelineItem";

export class MindMap implements TimelineItem {
    archived: boolean = false;
    attachments: number[] = []
    categoryId: number = -1
    cloudId: string | null = null;
    colorIndex: number = 0;
    content: string = "";
    createdDate: number = Date.now()
    deleted: boolean = false;
    deletedDate: number | null = null;
    description: string = "";
    editedDate: number = Date.now();
    id: number | null = null;
    permanentlyDeleted: boolean = false;
    pinned: boolean = false;
    reminderId: number = -1;
    tags: number[] = [];
    text: string = "";
    title: string = "";


    constructor(content: string, id: number, text: string, title: string) {
        this.content = content;
        this.id = id;
        this.text = text;
        this.title = title;
    }
}
