import {MindMap} from "../model/MindMap";
import {Note} from "../model/Note";
import {Reminder} from "../model/Reminder";
import {Category} from "../model/Category";
import {Tag} from "../model/Tag";
import {ThemeType} from "../ui/Themes";

export function getNextId(items: Array<Note | MindMap | Reminder | Category | Tag>): number {
    let id = 0
    items.forEach(item => {
        if (item.id != null && item.id > id) {
            id = item.id
        }
    })
    id += 1
    return id
}

export const lightColorsList = [
    "#FFFFFF",
    "#FFA4A4",
    "#FF95B9",
    "#E2A1ED",
    "#90CAF9",
    "#81D4FA",
    "#80DEEA",
    "#A5D6A7",
    "#C5E1A5",
    "#E6EE9C",
    "#FFE082",
    "#FFAB91",
]

export const darkColorsList = [
    "#202020",
    "#6E3838",
    "#63283C",
    "#5E3465",
    "#3C6D95",
    "#307898",
    "#276971",
    "#3E6040",
    "#54673E",
    "#73783C",
    "#8F782C",
    "#8E4D39",
]

export const getColorForIndex = (index: number, themeType: ThemeType) => {
    if (themeType === ThemeType.Light) {
        return lightColorsList[index]
    } else {
        return darkColorsList[index]
    }
}
