import {SyncFile} from "../model/SyncFile";
import {resolveType} from "./SyncHelper";


function convertToSyncFile(obj: { id: string; name: string; properties: any; }): SyncFile {
    const props = obj.properties
    return {
        id: obj.id,
        name: obj.name,
        type: resolveType(props.type),
        createdDate: parseInt(props.createdTime),
        editedDate: parseInt(props.modifiedTime),
        deletedDate: props.deletedTime === "null" || props.deletedTime.length === 0 ? null : parseInt(props.deletedTime),
        isDeleted: props.isDeleted === "true",
    } as SyncFile
}

export async function getFilesList() {
    try {
        // @ts-ignore
        return window.gapi.client.drive.files.list({
            "kind": "drive#fileList",
            "incompleteSearch": false,
            "spaces": ["appDataFolder"],
            'fields': "nextPageToken, files(id, name,properties)",
            "files": []
        }).then(function (response: { result: { files: any; }; }) {
            const files = response.result.files;
            // console.log(files) files list retrieved from cloud
            return files.map((file: { id: string; name: string; properties: any; }) => {
                return convertToSyncFile(file)
            })
        });
    } catch (e) {
        console.log(e)
        return []
    }
}

export async function getFileFromCloud(file: SyncFile) {
    try {
        // @ts-ignore
        return window.gapi.client.drive.files.get({
            fileId: file.id,
            alt: "media"
        }).then((resp: any) => {
            console.warn(resp)
            return resp.result
        });
    } catch (e) {
        console.log(e)
        return null
    }
}

export async function uploadJsonToCloud(
    syncFile: SyncFile,
    json: string,
) {
    try {
        let fileData = {
            resource: {
                name: syncFile.name,
                mimeType: "application/json",
                media: {
                    mediaType: "application/json",
                    body: json
                },
                fields: 'id',
                parents: ["appDataFolder"],
                properties: {
                    "type": syncFile.type,
                    "createdTime": syncFile.createdDate.toString(),
                    "modifiedTime": syncFile.editedDate.toString(),
                    "deletedTime": (syncFile.deletedDate || "null").toString(),
                    "isDeleted": syncFile.isDeleted ? "true" : "false",
                }
            }
        }
        if (syncFile.id == null || syncFile.id === "") {
            // @ts-ignore
            return window.gapi.client.drive.files.create(fileData).then((resp: any) => {
                console.log("Created File")
                console.log(resp)
                return resp
            })
        } else {
            // @ts-ignore
            return window.gapi.client.drive.files.put(fileData).then((resp: any) => {
                console.log("Updated File")
                console.log(resp)
                return resp
            })
        }
    } catch (e) {
        return null
    }
}
