import {Tag} from "../Tag";
import {db} from "./AppDatabase";

/**
 * Gets All Tags From Database
 */
export async function getAllTags() {
    // @ts-ignore
    return await db.tags.toArray()
}


/**
 * Adds a tag into database
 * @param item
 * @constructor
 */
export async function AddTagIntoDb(item: Tag) {
    // @ts-ignore
    await db.tags.add(item).catch(e => {
        console.warn("Error Adding Tag To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Adds a tag into database
 * @param item
 * @constructor
 */
export async function UpdateTagIntoDb(item: Tag) {
    // @ts-ignore
    await db.tags.put(item).catch(e => {
        console.warn("Error Adding Tag To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Deletes A Tag In Db
 * @param item
 * @constructor
 */
export async function DeleteTagInDb(item: Tag) {
    // @ts-ignore
    await db.tags.where("id").equals(item.id).delete().catch((e) => {
        console.warn("Error Deleting A Tag In Db")
        console.log(e)
        console.log(item)
    })
}