import "./App.scss";

import React from "react";
import {HashRouter, Route, Switch} from "react-router-dom";
import Main from "./ui/screens/Main";

import {User, UserProvider} from "./store/UserProvider";
import {createMuiTheme, ThemeProvider} from "@material-ui/core";
import {dark, light, ThemeType} from "./ui/Themes";
import Header from "./ui/components/Header";
import SideDrawer from "./ui/components/SideDrawer";
import Container from "./ui/components/Container";
import {initClient} from "./utils/AuthHelper";
import {onAuthStateChanged} from "./utils/FirebaseHelper";
import {StorageProvider} from "./store/StorageProvider";
import NoteEditor from "./ui/screens/NoteEditor/NoteEditor";
import {Note} from "./model/Note";
import {getAllNotes} from "./model/database/NoteDao";
import {AppStateProvider} from "./store/AppStateProvider";
import Reminders from "./ui/screens/Reminders";
import Tags from "./ui/screens/Tags";
import Categories from "./ui/screens/Categories";
import Trash from "./ui/screens/Trash";
import {MindMap} from "./model/MindMap";
import {Reminder} from "./model/Reminder";
import {Category} from "./model/Category";
import {Tag} from "./model/Tag";
import {getAllMindMaps} from "./model/database/MindMapDao";
import {getAllReminders} from "./model/database/ReminderDao";
import {getAllCategories} from "./model/database/CategoryDao";
import {getAllTags} from "./model/database/TagDao";
import Archive from "./ui/screens/Archive";
import SettingsDialog from "./ui/components/dialogs/SettingsDialog";
import Cookies from "js-cookie"
import AccountDialog from "./ui/components/dialogs/AccountDialog";
import {sortRemindersList} from "./utils/ReminderHelper";

type AppPropsType = {}

type AppStateType = {
    user: User | null,
    storage: {
        notes: Array<Note>,
        mindmaps: Array<MindMap>,
        reminders: Array<Reminder>,
        categories: Array<Category>,
        tags: Array<Tag>
    },
    appState: {
        themeType: ThemeType,
        drawerOpen: boolean,
        settingsOpen: boolean,
        accountOpen: boolean
    }
}

class App extends React.Component<AppPropsType, AppStateType> {

    getSavedTheme = () => {
        let saved = Cookies.get("theme")
        if (saved != null) {
            return saved as ThemeType
        } else {
            return ThemeType.Light
        }
    }

    state = {
        user: null,
        storage: {
            notes: [],
            mindmaps: [],
            reminders: [],
            categories: [],
            tags: []
        },
        appState: {
            themeType: this.getSavedTheme(),
            drawerOpen: false,
            settingsOpen: false,
            accountOpen: false,
        }
    };


    /**
     * Setups up GAPI Script Tag , Appends it to body and initializes client
     */
    setupGapi = () => {
        const script = document.createElement("script");
        script.setAttribute("async", "async");
        script.setAttribute("defer", "defer");
        script.src = "https://apis.google.com/js/api.js";
        script.onload = () => {
            if (window.gapi) {
                try {
                    window.gapi.load("client:auth2", initClient);
                } catch (exception) {
                    console.log(exception);
                }
            }
        };
        document.body.appendChild(script);
    }

    //User State Functions
    setUser = (user: User | null) => {
        this.setState({
            user
        })
    }

    //App State Functions
    setThemeType = (themeType: ThemeType) => {
        this.setState({
            appState: {
                ...this.state.appState,
                themeType
            }
        })
        Cookies.set("theme", themeType)
    }

    setDrawerOpen = (drawerOpen: boolean) => {
        this.setState({
            appState: {
                ...this.state.appState,
                drawerOpen
            }
        })
    }

    setSettingsOpen = (settingsOpen: boolean) => {
        this.setState({
            appState: {
                ...this.state.appState,
                settingsOpen
            }
        })
    }

    setAccountOpen = (accountOpen: boolean) => {
        this.setState({
            appState: {
                ...this.state.appState,
                accountOpen
            }
        })
    }

    //Storage State Functions
    initNotes = () => {
        getAllNotes().then((notes: Array<Note>) => {
            this.setState({
                storage: {
                    ...this.state.storage,
                    notes: notes.filter((note: Note) => !note.permanentlyDeleted)
                }
            })
        })
    }
    initMindMaps = () => {
        getAllMindMaps().then((mindmaps: Array<MindMap>) => {
            this.setState({
                storage: {
                    ...this.state.storage,
                    mindmaps: mindmaps.filter((mindmap: MindMap) => !mindmap.permanentlyDeleted)
                }
            })
        })
    }
    initReminders = () => {
        getAllReminders().then((reminders: Array<Reminder>) => {
            this.setState({
                storage: {
                    ...this.state.storage,
                    reminders: sortRemindersList(reminders.filter((reminder: Reminder) => !reminder.deleted
                    ))
                }
            })
        })
    }
    initCategories = () => {
        getAllCategories().then((categories: Array<Category>) => {
            this.setState({
                storage: {
                    ...this.state.storage,
                    categories: categories.filter((category: Category) => !category.deleted).reverse()
                }
            })
        })
    }
    initTags = () => {
        getAllTags().then((tags: Array<Tag>) => {
            this.setState({
                storage: {
                    ...this.state.storage,
                    tags: tags.filter((tag: Tag) => !tag.deleted).reverse()
                }
            })
        })
    }


    componentDidMount() {
        //Setting Up Authentication
        onAuthStateChanged((user) => {
            this.setState({
                user,
            });
        });
        this.setupGapi();

        //Setting Up Storage
        this.initNotes()
        this.initMindMaps()
        this.initReminders()
        this.initCategories()
        this.initTags()
    }

    render() {

        let theme
        if (this.state.appState.themeType === ThemeType.Dark) {
            theme = createMuiTheme(dark)
        } else {
            theme = createMuiTheme(light)
        }

        return (
            <ThemeProvider theme={theme}>
                <UserProvider user={{user: this.state.user, setUser: this.setUser}}>
                    <StorageProvider storage={{
                        ...this.state.storage,
                        initNotes: this.initNotes,
                        initMindMaps: this.initMindMaps,
                        initReminders: this.initReminders,
                        initCategories: this.initCategories,
                        initTags: this.initTags
                    }}>
                        <AppStateProvider
                            state={{
                                themeType: this.state.appState.themeType,
                                setThemeType: this.setThemeType,
                                drawerOpen: this.state.appState.drawerOpen,
                                setDrawerOpen: this.setDrawerOpen,
                                settingsOpen: this.state.appState.settingsOpen,
                                setSettingsOpen: this.setSettingsOpen,
                                accountOpen: this.state.appState.accountOpen,
                                setAccountOpen: this.setAccountOpen,
                            }}>
                            <HashRouter>
                                <Container className={theme.palette.type}>
                                    <Header/>
                                    <SideDrawer/>
                                    <Switch>
                                        <Route path="/note/:id?">
                                            <NoteEditor/>
                                        </Route>
                                        <Route path={"/reminders"}>
                                            <Reminders/>
                                        </Route>
                                        <Route path={"/categories"}>
                                            <Categories/>
                                        </Route>
                                        <Route path={"/tags"}>
                                            <Tags/>
                                        </Route>
                                        <Route path={"/archive"}>
                                            <Archive/>
                                        </Route>
                                        <Route path={"/trash"}>
                                            <Trash/>
                                        </Route>
                                        <Route path="/">
                                            <Main/>
                                        </Route>
                                    </Switch>

                                    {/*    Dialods    */}
                                    <AccountDialog
                                        open={this.state.appState.accountOpen}
                                        setOpen={this.setAccountOpen}
                                    />
                                    <SettingsDialog
                                        open={this.state.appState.settingsOpen}
                                        setOpen={this.setSettingsOpen}
                                    />
                                </Container>
                            </HashRouter>
                        </AppStateProvider>
                    </StorageProvider>
                </UserProvider>
            </ThemeProvider>
        );
    }
}

export default App;
