import {Note} from "../Note";
import {db} from "./AppDatabase";


/**
 * Gets All Notes From Database
 */
export async function getAllNotes() {
    // @ts-ignore
    return await db.notes.toArray()
}


export async function getNoteById(id: number) {
    // @ts-ignore
    return await db.notes.where("id").equals(id).first()
}

/**
 * Adds a note into database
 * @param item
 * @constructor
 */
export async function AddNoteIntoDb(item: Note) {
    // @ts-ignore
    await db.notes.add(item).catch(e => {
        console.warn("Error Adding Note To Db")
        console.log(e)
        console.log(item)
    })
}

/**
 * Adds a note into database
 * @param item
 * @constructor
 */
export async function UpdateNoteIntoDb(item: Note) {
    // @ts-ignore
    await db.notes.put(item).catch(e => {
        console.warn("Error Adding Note To Db")
        console.log(e)
        console.log(item)
    })
}
