import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import {signIn, signOut} from "../../utils/AuthHelper";
import React from "react";
import {createStyles, fade, makeStyles} from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";

import Typography from "@material-ui/core/Typography";

import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import {Avatar, Button, InputBase} from "@material-ui/core";
import useUser from "../../store/UserProvider";
import useAppState from "../../store/AppStateProvider";
import {Brightness4, Brightness7, Search} from "@material-ui/icons";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import {ThemeType} from "../Themes";

const useStyles = makeStyles((theme) => createStyles({
    appBar: {
        [theme.breakpoints.up('sm')]: {
            width: `100%`,
        },
        background: theme.palette.background.default,
        boxShadow: "0px 0px 0px 0px",
        borderBottom: "1px solid " + theme.palette.divider
    },
    menuButton: {
        marginRight: theme.spacing(2),
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    },
    //Search Styles
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },
    //Custom Styles
    rightContainer: {
        marginLeft: "auto",
    },
    loginButton: {
        textTransform: "none",
    },
    profileImage: {
        width: "40px",
        height: "40px",
        borderRadius: "50%",
    },
    appBarContainer: {
        background: theme.palette.background.default,
    }
}));

export default function Header() {
    const classes = useStyles();
    const {user, setUser} = useUser();
    const isSignedIn = user != null;
    const handleSignIn = () => {
        signIn();
    };

    // Profile Menu
    const [anchorEl, setAnchorEl] = React.useState<any | null>(null);
    const [anchorMainEl, setAnchorMainEl] = React.useState<any | null>(null);
    const {setSettingsOpen, setAccountOpen} = useAppState()

    const handleClick = (event: { currentTarget: any; }) => {
        setAnchorEl(event.currentTarget);
    };

    const handleMainClick = (event: { currentTarget: any; }) => {
        setAnchorMainEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleMainClose = () => {
        setAnchorMainEl(null);
    }

    const {drawerOpen, setDrawerOpen} = useAppState()

    //Handling Profile Menu Item Click
    const handleAccount = () => {
        //Todo goto account page or something
    };
    const handleLogout = () => {
        signOut();
        setUser(null);
    };
    return (
        <AppBar
            position="fixed"
            className={`${classes.appBar}`}
            color={"transparent"}
        >
            <Toolbar>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={() => {
                        setDrawerOpen(!drawerOpen)
                    }}
                    className={classes.menuButton}
                >
                    <MenuIcon/>
                </IconButton>
                <Typography variant="h6" noWrap>
                    Timeline
                </Typography>
                <div className={classes.search}>
                    <div className={classes.searchIcon}>
                        <Search/>
                    </div>
                    <InputBase
                        placeholder="Search"
                        classes={{
                            root: classes.inputRoot,
                            input: classes.inputInput,
                        }}
                        inputProps={{'aria-label': 'search'}}
                    />
                </div>
                <div className={classes.rightContainer}>
                    <ThemeToggle/>
                    {!isSignedIn ? (
                        <Button
                            color="inherit"
                            className={classes.loginButton}
                            onClick={handleSignIn}
                        >
                            Login
                        </Button>
                    ) : (
                        <IconButton
                            aria-controls="simple-menu"
                            aria-haspopup="true"
                            onClick={handleClick}
                        >
                            <Avatar
                                src={user?.photoURL}
                                alt={user?.displayName}
                            />
                        </IconButton>
                    )}
                    <IconButton
                        aria-controls="simple-menu"
                        aria-haspopup="true"
                        onClick={handleMainClick}
                    >
                        <MoreVertIcon/>
                    </IconButton>
                </div>
                <Menu
                    id="simple-menu"
                    anchorEl={anchorEl}
                    keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleClose}
                >
                    <MenuItem onClick={handleClose}>My Account</MenuItem>
                    <MenuItem
                        onClick={(e) => {
                            handleClose();
                            handleLogout();
                        }}
                    >
                        Logout
                    </MenuItem>
                </Menu>
                <Menu
                    id="simple-menu"
                    anchorEl={anchorMainEl}
                    keepMounted
                    open={Boolean(anchorMainEl)}
                    onClose={handleMainClose}
                >
                    <MenuItem onClick={() => {
                        setAccountOpen(true)
                    }}>Account</MenuItem>
                    <MenuItem onClick={() => {
                        setSettingsOpen(true)
                    }}>Settings</MenuItem>
                </Menu>
            </Toolbar>
        </AppBar>
    );
}

export function ThemeToggle() {

    const {themeType, setThemeType} = useAppState()

    return (
        <IconButton
            onClick={() => {
                if (themeType === ThemeType.Light) {
                    setThemeType(ThemeType.Dark)
                } else {
                    setThemeType(ThemeType.Light)
                }
            }}>
            {themeType === ThemeType.Dark ? (
                <Brightness7/>
            ) : (
                <Brightness4/>
            )}
        </IconButton>
    )
}
